import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/purchase/purchaseInvoice/actions";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";

import {
  Modal,
  Tooltip,
  Table,
  Collapse,
  Select,
  DatePicker,
  Input,
  Row,
  Col,
  Empty
} from "antd";
import { StatusTag, customCollapseStyle, StatusBadge, TransNo } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;
const Search = Input.Search;
const { getPurchaseReceipts } = actions;

const expandedRowRender = data => {
  const columns = [
    {
      title: getLanguage({ id: "no." }),
      dataIndex: "index_no",
      width: "5%"
    },
    {
      title: getLanguage({ id: "code" }),
      dataIndex: "item_code",
      width: "15%",
      render: text => {
        if (text && text.length > 25) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 25) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({ id: "itemName" }),
      dataIndex: "item_name",
      width: "30%",
      render: text => {
        if (text && text.length > 50) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 50) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="purchaseOrderQuantity" />}>
          <a href="javascript:;"><IntlMessages id="poQty" /></a>
        </Tooltip>
      ),
      dataIndex: "ordered_qty", // dari PR
      width: "10%",
      align: "right",
      render: (text, row) => <><NumberFormat value={text} /> {row.unit_code}</>
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="receivedQty" />}>
          <a href="javascript:;"><IntlMessages id="rcvdQty" /></a>
        </Tooltip>
      ),
      dataIndex: "po_detail.received_qty", // dari PO
      width: "10%",
      align: "right",
      render: (text, row) => <><NumberFormat value={text} /> {row.unit_code}</>
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="leftQuantity" />}>
          <a href="javascript:;"><IntlMessages id="leftQty" /></a>
        </Tooltip>
      ),
      dataIndex: "left_qty", // dari PR
      width: "10%",
      align: "right",
      render: (text, row) => <><NumberFormat value={text} /> {row.unit_code}</>
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="receiveQuantity" />}>
          <a href="javascript:;"><IntlMessages id="receiveQty" /></a>
        </Tooltip>
      ),
      dataIndex: "receive_qty", // dari PR
      width: "10%",
      align: "right",
      render: (text, row) => <><NumberFormat value={text} /> {row.unit.unit_code}</>
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="baseQuantity" />}>
          <a href="javascript:;"><IntlMessages id="baseQty" /></a>
        </Tooltip>
      ),
      dataIndex: "base_qty", // dari PR
      width: "10%",
      align: "right",
      render: (text, row) => <><NumberFormat value={text} /> {row.base_unit.unit_code}</>
    }
  ];

  const dataSource = data;
  return (
    <div>
      <strong>
        <IntlMessages id="purchaseInvoices.importPR.itemList" />
      </strong>

      <Table
        rowKey={record => record.index_no}
        size="small"
        columns={columns}
        dataSource={dataSource}
        pagination={true}
        style={{ marginTop: "24px" }}
      />
    </div>
  );
};

class ModalPurchaseReceipt extends Component {
  state = {
    filter: {
      id_supplier: this.props.id_supplier,
      id_receipt_location: this.props.id_location,
      id_purchase_order: null,
      status: 2,
      start_date: null,
      end_date: null,
      pagination: null
    },
    search: null
  };

  componentDidUpdate(prevProps) {
    const { id_location, id_supplier } = this.props;

    if (
      prevProps.id_location !== id_location &&
      prevProps.id_supplier !== id_supplier &&
      id_location &&
      id_supplier
    ) {
      this.handleLocationSupplierChange(id_location, id_supplier);
      this.setState({
        filter: {
          ...this.state.filter,
          id_supplier: id_supplier,
          id_receipt_location: id_location
        }
      });
    } else if (
      prevProps.id_location !== id_location &&
      id_supplier &&
      id_location
    ) {
      this.handleLocationSupplierChange(id_location, id_supplier);
      this.setState({
        filter: {
          ...this.state.filter,
          id_supplier: id_supplier,
          id_receipt_location: id_location
        }
      });
    } else if (
      prevProps.id_supplier !== id_supplier &&
      id_location &&
      id_supplier
    ) {
      this.handleLocationSupplierChange(id_location, id_supplier);
      this.setState({
        filter: {
          ...this.state.filter,
          id_supplier: id_supplier,
          id_receipt_location: id_location
        }
      });
    }
  }

  handleLocationSupplierChange = (id_location, id_supplier) => {
    const { getPurchaseReceipts, transaction_date } = this.props;
    const string =
      `id_receipt_location=${id_location}&id_supplier=${id_supplier}&transaction_date=${transaction_date}`;

    getPurchaseReceipts(1, null, string);
  };

  handleLocationChange = id_location => {
    const { getPurchaseReceipts } = this.props;
    const { id_supplier } = this.state.filter;
    let string = "id_receipt_location=" + id_location;
    if (id_supplier) {
      string += "&id_supplier=" + id_supplier;
    }
    getPurchaseReceipts(1, null, string);
  };

  handleSupplierChange = id_supplier => {
    const { getPurchaseReceipts } = this.props;
    const { id_receipt_location } = this.state.filter;
    let string = "id_supplier=" + id_supplier;
    if (id_receipt_location) {
      string += "&id_receipt_location=" + id_receipt_location;
    }
    getPurchaseReceipts(1, null, string);
  };

  handleSelect = value => {
    const { handleSelectModal } = this.props;
    handleSelectModal(value);
  };

  // Handle search
  // handleSearch = value => {
  //   const { getPurchaseReceipts } = this.props;
  //   getPurchaseReceipts(1, value);
  // };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    this.setState({
      filter: {
        id_supplier: this.props.id_supplier,
        id_receipt_location: this.props.id_location,
        id_purchase_order: null,
        status: 2,
        start_date: null,
        end_date: null,
        pagination: null
      },
      search: null
    }, () => this.handleFilter());
  };

  //Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  // Handle filter
  handleFilter = () => {
    const { getPurchaseReceipts, transaction_date } = this.props;
    //const { current_page_pr } = this.props.customer;
    const { filter, search } = this.state;
    let string = "";

    if (filter.id_receipt_location) {
      string += "id_receipt_location=" + filter.id_receipt_location;
    }
    // if (filter.status) {
    //   string
    //     ? (string += "&status=" + filter.status)
    //     : (string += "status=" + filter.status);
    // }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
          "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
          "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    } else {
      string
        ? (string +=
            "&transaction_date=" + transaction_date)
        : (string +=
            "transaction_date=" + transaction_date);
    }
    if (search) {
      string
        ? (string += "&search=" + search)
        : (string += "search=" + search);
    }
    if (filter.id_supplier) {
      string
        ? (string += "&id_supplier=" + filter.id_supplier)
        : (string += "id_supplier=" + filter.id_supplier);
    }
    if (filter.id_purchase_order) {
      string
        ? (string += "&id_purchase_order=" + filter.id_purchase_order)
        : (string += "id_purchase_order=" + filter.id_purchase_order);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }
    // console.log(string);
    // this.setState({ search: null });
    getPurchaseReceipts(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    this.setState({
      filter: {
        id_supplier: this.props.id_supplier,
        id_receipt_location: this.props.id_location,
        id_purchase_order: null,
        status: 2,
        start_date: null,
        end_date: null,
        pagination: null
      },
      search: null
    }, () => this.handleFilter());
  };

  // Handle pagination
  handlePageChange = page => {
    const { getPurchaseReceipts } = this.props;
    const { search, filter } = this.props.purchaseInvoice;
    getPurchaseReceipts(page, search, filter);
  };

  render() {
    // console.log(this.props);
    const { visible, handleModal, purchaseInvoice, transaction_date } = this.props;
    const {
      purchaseReceipts,
      // purchaseOrders,
      locations,
      suppliers
    } = purchaseInvoice ? purchaseInvoice : [];
    const { loadingForeign, loadingPurchaseReceipts } = purchaseInvoice
      ? purchaseInvoice
      : false;

    const columns = [
      {
        title: getLanguage({ id: "purchaseReceipts.purchaseReceiptNo" }),
        dataIndex: "purchase_receipt_no",
        rowKey: "purchase_receipt_no",
        width: "15%",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "new";
              break;
            case 2:
              className = "approved";
              break;
            case 3:
              className = "rejected";
              break;
            case 4:
              className = "invoiced";
              break;
            case 5:
              className = "cancelled";
              break;
            default:
              className = "new";
              break;
          }
  
          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <Tooltip
                placement="bottom"
                title={<IntlMessages id="tooltip.select" />}
              >
                <a
                  onClick={e => {
                    e.preventDefault();
                    this.handleSelect(row.id_purchase_receipt);
                  }}
                  href="javascript:;"
                >
                  <TransNo>{text}</TransNo>
                </a>
              </Tooltip>
            </div>
          );
          // return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "purchaseOrders.purchaseOrderNo" }),
        dataIndex: "purchase_order_no",
        rowKey: "purchase_order_no",
        width: "15%"
      },
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "purchase_receipt_date",
        rowKey: "purchase_receipt_date",
        width: "15%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "sidebar.supplier" }),
        dataIndex: "supplier_name",
        rowKey: "supplier_name",
        width: "15%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        width: "15%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "10%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status",
        rowKey: "status",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
            case 2:
              className = "approved";
              display = getLanguage({ id: "approved" });
              break;
            case 3:
              className = "rejected";
              display = getLanguage({ id: "rejected" });
              break;
            case 4:
              className = "invoiced";
              display = getLanguage({ id: "invoiced" });
              break;
            case 5:
              className = "cancelled";
              display = getLanguage({ id: "cancelled" });
              break;
            default:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
          }
  
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    const dataSource = [];
    Object.keys(purchaseReceipts).map((item, index) => {
      return dataSource.push({
        ...purchaseReceipts[item],
        key: item
      });
    });

    const { current_page_pr, page_size_pr, total_pr } = this.props
      .purchaseInvoice
      ? this.props.purchaseInvoice
      : [];

    return (
      <Modal
        title={<IntlMessages id="purchaseInvoices.importPurchaseReceipt" />}
        visible={visible}
        centered
        footer={null}
        keyboard={true}
        onCancel={handleModal}
        width="95%"
      >
        <Row style={{ marginBottom: "20px" }} type="flex">
          <Col span={18}>
            <Search
              placeholder={
                `${getLanguage({ id: "purchaseReceipts.purchaseReceiptNo" })}, ` +
                `${getLanguage({ id: "createdBy" })}`
              }
              onSearch={this.handleSearch}
              style={{ width: "99%", height: "36px" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
          </Col>
          <Col span={6}>
            <Button
              type="primary"
              onClick={this.handleFilter}
              style={{ marginRight: "2%" }}
            >
              <IntlMessages id="button.search" />
            </Button>

            <Button onClick={this.handleResetSearch}>
              <IntlMessages id="filter.reset" />
            </Button>
          </Col>
        </Row>
        <Row style={{ marginBottom: "20px" }}>
          <Col>
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Input.Group>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="suppliers.pageHeader" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_supplier"
                        )}
                        value={
                          this.state.filter.id_supplier
                            ? this.state.filter.id_supplier
                            : []
                        }
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="purchaseReceipts.filter.select.supplier" />
                        }
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                        disabled={true}
                      //showArrow={false}
                      >
                        {suppliers.map((data, key) => (
                          <Option key={key} value={data.id_supplier}>
                            {`${data.supplier_code} (${data.supplier_name})`}
                          </Option>
                        ))}
                      </Select>
                    </Col>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="locations.pageHeader" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_receipt_location"
                        )}
                        value={
                          this.state.filter.id_receipt_location
                            ? this.state.filter.id_receipt_location
                            : []
                        }
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="purchaseReceipts.filter.select.location" />
                        }
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                        disabled={true}
                      //showArrow={false}
                      >
                        {locations.map((data, key) => (
                          <Option key={key} value={data.id_location}>
                            {`${data.location_code} (${data.location_name})`}
                          </Option>
                        ))}
                      </Select>
                    </Col>
                  </Input.Group>
                </Row>
                <Row
                  type="flex"
                  justify="start"
                  style={{ marginBottom: "24px" }}
                >
                  <Input.Group>
                    {/* <Col span={12}>
                      <label>
                        <IntlMessages id="purchaseOrders.pageHeader" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_purchase_order"
                        )}
                        value={
                          this.state.filter.id_purchase_order
                            ? this.state.filter.id_purchase_order
                            : []
                        }
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="purchaseReceipts.filter.select.purchaseOrder" />
                        }
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                      //showArrow={false}
                      >
                        {purchaseOrders.map((data, key) => (
                          <Option key={key} value={data.id_purchase_order}>
                            {data.purchase_order_no}
                          </Option>
                        ))}
                      </Select>
                    </Col> */}
                    <Col span={12}>
                      <label>
                        <IntlMessages id="purchaseReceipts.filter.date" />
                      </label>
                      <br />
                      <RangePicker
                        format="DD MMMM YYYY HH:mm:ss"
                        showTime
                        allowClear={true}
                        style={{ width: "100%" }}
                        value={
                          this.state.filter.start_date &&
                            this.state.filter.end_date
                            ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                            : []
                        }
                        onChange={this.handleFilterChange.bind(this, "date")}
                        disabledDate={current =>
                          current > moment(transaction_date).endOf("day")
                        }
                      />
                    </Col>
                    <Col span={12}>
                      <Row type="flex" justify="start">
                        <Col span={8}>
                          <label>
                            <IntlMessages id="dataPerPage" />
                          </label>
                          <br />
                          <Select
                            showSearch
                            onChange={this.handleFilterChange.bind(
                              this,
                              "pagination"
                            )}
                            value={
                              this.state.filter.pagination
                                ? this.state.filter.pagination
                                : page_size_pr
                            }
                            style={{ width: "80%" }}
                            // placeholder={
                            //   <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                            // }
                            optionFilterProp="children"
                            filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                          >
                            <Option key={1} value={10}>
                              10
                            </Option>
                            <Option key={2} value={25}>
                              25
                            </Option>
                            <Option key={3} value={50}>
                              50
                            </Option>
                            <Option key={4} value={100}>
                              100
                            </Option>
                          </Select>
                        </Col>

                        <Col
                          span={8}
                          style={{
                            marginTop: "auto"
                          }}
                        >
                          <Button
                            size="small"
                            type="primary"
                            style={{ marginRight: "10px" }}
                            onClick={this.handleFilter}
                          >
                            <IntlMessages id="filter.submit" />
                          </Button>
                          <Button onClick={this.handleResetFilter} size="small">
                            <IntlMessages id="filter.reset" />
                          </Button>
                        </Col>
                      </Row>
                    </Col>
                  </Input.Group>
                </Row>
              </Panel>
            </Collapse>
          </Col>
        </Row>
        <Row>
          <Col>
            {dataSource.length === 0 && !loadingPurchaseReceipts ? (
              <Empty
                description={
                  <IntlMessages id="purchaseReceipts.table.noData" />
                }
              />
            ) : (
                <Table
                  dataSource={dataSource}
                  columns={columns}
                  loading={loadingPurchaseReceipts}
                  expandedRowRender={record =>
                    expandedRowRender(record.purchase_receipt_details)
                  }
                  expandIcon={props => (
                    <Button
                      onClick={e => props.onExpand(props.record, e)}
                      size="small"
                    >
                      <IntlMessages id="button.view" />
                    </Button>
                  )}
                  // Set pagination, jika ada page_size_pr mengunakan pagination API
                  // jika tidak ada page_size_pr menggunakan pagination bawaan
                  pagination={
                    page_size_pr
                      ? {
                        current: current_page_pr,
                        pageSize: parseInt(page_size_pr),
                        total: total_pr,
                        onChange: this.handlePageChange.bind(this)
                      }
                      : true
                  }
                />
              )}
          </Col>
        </Row>
      </Modal>
    );
  }
}

export default compose(
  connect(
    null,
    { getPurchaseReceipts }
  )
)(ModalPurchaseReceipt);
