import React, { Component } from "react";

import { Button, Popconfirm, Tooltip, Form } from "antd";
import EditableCell from "./editableCellFreightCost";
import EditableContext from "./editableContext";
import "../../../App/editableStyle.css";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper from "../../../App/app.style";
import { getSelectedItems } from "./updateForm";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

const getTotal = arr => {
  let totalCurrencyAmount = 0;
  let totalAmount = 0;
  arr.map(data => {
    totalCurrencyAmount += parseFloat(data.currency_amount);
    totalAmount += parseFloat(data.amount);
  });

  return {
    totalCurrencyAmount: totalCurrencyAmount,
    totalAmount: totalAmount
  };
};

class EditableTable extends Component {
  constructor(props) {
    super(props);
    const { freightCosts } = props;

    this.state = {
      dataSource: [...freightCosts],
      count: freightCosts.length + 1,
      totalCurrencyAmount: 0,
      totalAmount: 0
    };
  }

  componentDidMount() {
    const {
      freightCost,
      freightCosts,
      clearFreightCost,
      clearFreightCosts
    } = this.props;

    if (freightCost) {
      const {
        dataSource,
        count,
        totalCurrencyAmount,
        totalAmount
      } = this.state;

      // const find = dataSource.findIndex(
      //   currData => currData.id_account === freightCost.id_account
      // );

      // freightCost baru
      // if (find < 0) {
      const init = {
        index_no: count,
        currency_amount: 0,
        amount: 0,
        freight_cost_items: [],
        selected_items: []
      };

      const newData = Object.assign({}, freightCost, init);

      this.setState({
        dataSource: [...dataSource, newData],
        count: count + 1
      });

      clearFreightCost(
        [...dataSource, newData],
        totalCurrencyAmount,
        totalAmount
      );
      // }
    }
    // jika freightCost dari transaksi sebelumnya
    else if (freightCosts.length !== 0) {
      let newTotCurrAmount = 0;
      let newTotAmount = 0;
      let newIndex = 0;

      freightCosts.map(currData => {
        currData.selected_items = getSelectedItems(
          this.props.items,
          currData.freight_cost_items
        );

        newTotCurrAmount += parseFloat(currData.currency_amount);
        newTotAmount += parseFloat(currData.amount);
        newIndex = parseInt(currData.index_no) + 1;
      });

      this.setState({
        dataSource: [...freightCosts],
        totalCurrencyAmount: newTotCurrAmount,
        totalAmount: newTotAmount,
        count: newIndex
      });
      clearFreightCosts(freightCosts, newTotCurrAmount, newTotAmount);
    }
  }

  componentDidUpdate(prevProps) {
    const {
      freightCost,
      freightCosts,
      items,
      currency_rate,
      handleSaveFreightCostList,
      clearFreightCost,
      clearFreightCosts
    } = this.props;

    // milih freightCost sendiri
    if (freightCost) {
      const {
        dataSource,
        count,
        totalCurrencyAmount,
        totalAmount
      } = this.state;

      // const find = dataSource.findIndex(
      //   currData => currData.id_account === freightCost.id_account
      // );

      // freightCost baru
      // if (find < 0) {
      const init = {
        index_no: count,
        cost_description: freightCost.cost_description,
        currency_amount: 0,
        amount: 0,
        freight_cost_items: [],
        selected_items: []
      };

      const newData = Object.assign({}, init);

      this.setState({
        dataSource: [...dataSource, newData],
        count: count + 1
      });

      clearFreightCost(
        [...dataSource, newData],
        totalCurrencyAmount,
        totalAmount
      );
      // }
    }
    // jika freightCost dari transaksi sebelumnya
    else if (freightCosts.length !== 0) {
      let newData = [];
      let newTotCurrAmount = 0;
      let newTotAmount = 0;
      let newIndex = 0;

      freightCosts.map(cost => {
        let init = {
          index_no: cost.index_no,
          cost_description: cost.cost_description
            ? cost.cost_description
            : cost.account_name,
          currency_amount: parseFloat(cost.currency_amount),
          amount: parseFloat(cost.currency_amount) * currency_rate,
          freight_cost_items: cost.freight_cost_items,
          selected_items: []
        };

        init.selected_items = getSelectedItems(
          this.props.items,
          init.freight_cost_items
        );

        newData.push(init);
        newTotCurrAmount += parseFloat(init.currency_amount);
        newTotAmount += parseFloat(init.amount);
        newIndex = parseInt(init.index_no) + 1;
      });

      this.setState({
        dataSource: [...newData],
        totalCurrencyAmount: newTotCurrAmount,
        totalAmount: newTotAmount,
        count: newIndex
      });
      clearFreightCosts(newData, newTotCurrAmount, newTotAmount);
    }

    /** Handle perubahan item */
    // Jika item ada yang dihapus
    if (prevProps.items.length !== items.length && prevProps.items.length > 0) {
      const { dataSource, totalCurrencyAmount, totalAmount } = this.state;

      let newFreightCosts = [...dataSource];
      newFreightCosts.map(currData => {
        currData.selected_items = getSelectedItems(
          this.props.items,
          currData.freight_cost_items
        );
        currData.freight_cost_items = currData.selected_items.map(data => ({
          index_no: data.index_no,
          id_item: data.id_item
        }));
      });

      this.setState({
        dataSource: [...newFreightCosts]
      });
      handleSaveFreightCostList(
        newFreightCosts,
        totalCurrencyAmount,
        totalAmount
      );
    }
    // Jika ada perubahan item
    else if (prevProps.items !== items && items.length > 0) {
      const { dataSource, totalCurrencyAmount, totalAmount } = this.state;

      let newFreightCosts = [...dataSource];
      newFreightCosts.map(currData => {
        currData.selected_items = getSelectedItems(
          this.props.items,
          currData.freight_cost_items
        );
        currData.freight_cost_items = currData.selected_items.map(data => ({
          index_no: data.index_no,
          id_item: data.id_item
        }));
      });

      this.setState({
        dataSource: [...newFreightCosts]
      });
      handleSaveFreightCostList(
        newFreightCosts,
        totalCurrencyAmount,
        totalAmount
      );
    }
    // jika ada perubahan currency rate
    else if (prevProps.currency_rate !== currency_rate) {
      const { dataSource } = this.state;
      let newTotCurrAmount = 0;
      let newTotAmount = 0;

      let newFreightCosts = [...dataSource];
      newFreightCosts.map(currData => {
        currData.amount = currData.currency_amount * parseFloat(currency_rate);

        newTotCurrAmount += parseFloat(currData.currency_amount);
        newTotAmount += parseFloat(currData.amount);
      });

      this.setState({
        dataSource: [...newFreightCosts],
        totalCurrencyAmount: newTotCurrAmount,
        totalAmount: newTotAmount
      });
      handleSaveFreightCostList(
        newFreightCosts,
        newTotCurrAmount,
        newTotAmount
      );
    }
  }

  handleDelete = index_no => {
    const { handleSaveFreightCostList } = this.props;
    let newTotCurrAmount = 0;
    let newTotAmount = 0;

    const dataSource = this.state.dataSource;

    let newData = dataSource.filter(currData => currData.index_no !== index_no);
    newData.map((currData, index) => {
      currData.index_no = index + 1;
      newTotCurrAmount += parseFloat(currData.currency_amount);
      newTotAmount += parseFloat(currData.amount);
    });

    this.setState({
      dataSource: [...newData],
      count: newData.length + 1,
      totalCurrencyAmount: newTotCurrAmount,
      totalAmount: newTotAmount
    });
    handleSaveFreightCostList(newData, newTotCurrAmount, newTotAmount);
  };

  handleSave = row => {
    const { handleSaveFreightCostList, currency_rate } = this.props;
    const rate = currency_rate ? parseFloat(currency_rate) : 1;
    const newData = this.state.dataSource;
    let newTotCurrAmount = 0;
    let newTotAmount = 0;

    const index = newData.findIndex(
      currData => row.index_no === currData.index_no
    );

    row.amount = parseFloat(row.currency_amount) * rate;

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    newData.map(currData => {
      newTotCurrAmount += parseFloat(currData.currency_amount);
      newTotAmount += parseFloat(currData.amount);
    });

    // console.log(row);

    this.setState({
      dataSource: [...newData],
      totalCurrencyAmount: newTotCurrAmount,
      totalAmount: newTotAmount
    });
    handleSaveFreightCostList(newData, newTotCurrAmount, newTotAmount);
  };

  render() {
    // console.log(this.state.dataSource);

    const { dataSource } = this.state;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const columns = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        width: "5%",
        align: "right",
        render: (text, row) => {
          if (isNaN(text)) {
            return {
              children: <strong>{text}</strong>,
              props: {
                colSpan: 2
              }
            };
          } else {
            return {
              children: text,
              props: {
                colSpan: 1
              }
            };
          }
        }
      },
      {
        title: getLanguage({ id: "label.description" }),
        dataIndex: "cost_description",
        width: "20%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          if (text && text.length > 50) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 50) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "amount" }),
        dataIndex: "currency_amount",
        width: "25%",
        align: "right",
        editable: true,
        onCell: record => {
          if (isNaN(record.index_no)) {
            return {
              record,
              dataIndex: "currency_amount",
              title: getLanguage({ id: "amount" }),
              width: "15%",
              colSpan: 1
            };
          } else {
            return {
              record,
              editable: true,
              dataIndex: "currency_amount",
              title: getLanguage({ id: "amount" }),
              width: "15%",
              handleSave: this.handleSave,
              status: this.props.status
            };
          }
        }
      },
      {
        title: getLanguage({ id: "baseAmount" }),
        width: "25%",
        dataIndex: "amount",
        align: "right",
        render: (text, row) => {
          const val = <NumberFormat value={text} />;
          if (isNaN(row.index_no)) {
            return {
              children: text,
              props: {
                colSpan: 1
              }
            };
          } else {
            return val;
          }
        }
      },
      {
        title: getLanguage({ id: "item" }),
        dataIndex: "item_name",
        width: "30%",
        align: "center",
        editable: true,
        onCell: record => {
          if (isNaN(record.index_no)) {
            return {
              colSpan: 2
            };
          } else {
            return {
              record,
              editable: true,
              items: this.props.items ? this.props.items : [],
              dataIndex: "item_name",
              title: getLanguage({ id: "item" }),
              width: "20%",
              handleSave: this.handleSave,
              status: this.props.status
            };
          }
        }
      },
      
    ];

    let operation =
      {
        title: "",
        dataIndex: "operation",
        width: "15%",
        align: "center",
        fixed: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          else {
            return (
              <Popconfirm
                title={<IntlMessages id="confirmDelete" />}
                onConfirm={() => this.handleDelete(row.index_no)}
                okText={<IntlMessages id="yes" />}
                cancelText={<IntlMessages id="no" />}
              >
                <Tooltip
                  title={<IntlMessages id="delete" />}
                >
                  <Button
                    //style={{ borderRadius: "50%" }}
                    className="invoiceDltBtn"
                    size="small"
                  >
                    <i className="ion-trash-b" />
                  </Button>
                </Tooltip>
              </Popconfirm>
            );
          }
        }
      }

    if ((!this.props.status || this.props.status === 1) && columns.length === 5){
      columns.push(operation)
    }

    const total = getTotal(dataSource);
    const info = {
      index_no: getLanguage({ id: "total" }),
      currency_amount: <NumberFormat value={total.totalCurrencyAmount} />,
      amount: <NumberFormat value={total.totalAmount} />
    };
    const modifiedData = dataSource.length > 0 ? [...dataSource, info] : [];
    // console.log(modifiedData, total);
    return (
      <CardWrapper>
        <Scrollbars style={{ width: "100%" }}>
          <TableWrapper
            className="invoiceListTable"
            components={components}
            rowKey={record => record.index_no}
            rowClassName={() => "editable-row"}
            dataSource={modifiedData}
            columns={columns}
            pagination={false}
            // scroll={{ x: 1300 }}
          />
        </Scrollbars>
      </CardWrapper>
    );
  }
}

export default EditableTable;
