const actions = {
  getPurchaseInvoices: (page, search, filter) => ({
    type: "GET_PURCHASE_INVOICES_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createPurchaseInvoice: () => ({
    type: "CREATE_PURCHASE_INVOICE_REQUEST"
  }),
  getItemsBySupplier: (id_supplier, id_location, date, search) => ({
    type: "GET_FILTER_ITEMS_BY_SUPPLIER_PI_REQUEST",
    payload: {
      id_supplier: id_supplier,
      id_location: id_location,
      date: date,
      search: search
    }
  }),
  getDiscounts: data => ({
    type: "GET_DISCOUNT_PI_REQUEST",
    payload: { body: data }
  }),
  getTransactions: (page, search, filter) => ({
    type: "GET_FILTER_PURCHASE_INVOICES_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getPurchaseReceipts: (page, search, filter) => ({
    type: "GET_FILTER_PURCHASE_RECEIPTS_PI_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  storePurchaseInvoice: data => ({
    type: "STORE_PURCHASE_INVOICE_REQUEST",
    payload: { body: data }
  }),
  editPurchaseInvoice: (id_purchase_invoice, search) => ({
    type: "EDIT_PURCHASE_INVOICE_REQUEST",
    payload: { id_purchase_invoice: id_purchase_invoice, search: search }
  }),
  updatePurchaseInvoice: (id_purchase_invoice, data) => ({
    type: "UPDATE_PURCHASE_INVOICE_REQUEST",
    payload: {
      id_purchase_invoice: id_purchase_invoice,
      body: data
    }
  }),
  showPurchaseInvoice: id_purchase_invoice => ({
    type: "SHOW_PURCHASE_INVOICE_REQUEST",
    payload: { id_purchase_invoice: id_purchase_invoice }
  }),
  confirmPurchaseInvoice: id_purchase_invoice => ({
    type: "CONFIRM_PURCHASE_INVOICE_REQUEST",
    payload: { id_purchase_invoice }
  }),
  unconfirmPurchaseInvoice: id_purchase_invoice => ({
    type: "UNCONFIRM_PURCHASE_INVOICE_REQUEST",
    payload: { id_purchase_invoice }
  }),
  closePurchaseInvoice: id_purchase_invoice => ({
    type: "CLOSE_PURCHASE_INVOICE_REQUEST",
    payload: { id_purchase_invoice }
  }),
  cancelPurchaseInvoice: (id_purchase_invoice, body) => ({
    type: "CANCEL_PURCHASE_INVOICE_REQUEST",
    payload: { id_purchase_invoice, body }
  }),
  uploadDocument: (id_purchase_invoice, data) => ({
    type: "UPLOAD_PURCHASE_INVOICE_DOCUMENT_REQUEST",
    payload: {
      id_purchase_invoice,
      body: data
    }
  }),
  downloadDocument: id_purchase_invoice_document => ({
    type: "DOWNLOAD_PURCHASE_INVOICE_DOCUMENT_REQUEST",
    payload: { id_purchase_invoice_document }
  }),
  deleteDocument: (id_purchase_invoice_document, id_purchase_invoice) => ({
    type: "DELETE_PURCHASE_INVOICE_DOCUMENT_REQUEST",
    payload: {
      id_purchase_invoice_document: id_purchase_invoice_document,
      id_purchase_invoice: id_purchase_invoice
    }
  }),
  setTaxBase: tax_bases => ({
    type: "SET_TAX_BASES_PURCHASE_INVOICE",
    payload: { tax_bases: tax_bases }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
