export const getSelectedItems = (items, arr_items) => {
  if (
    Array.isArray(arr_items) &&
    arr_items.length > 0 &&
    typeof arr_items[0] === "string"
  ) {
    // if (arr_items.length > 0 && typeof arr_items[0] === "string") {
    return items.filter(data => arr_items.includes(data.id_item));
    // }
  } else {
    let filtered = [];
    if (Array.isArray(items)) {
      items.map(currData => {
        const [selected] = Array.isArray(arr_items)
          ? arr_items.filter(data => data.index_no === currData.index_no)
          : {};
        if (selected) {
          filtered.push(currData);
        }
      });
    }
    return filtered;
  }
};

class PurchaseInvoiceUpdateForm extends Component {
  state = {
    id_location: null,
    id_supplier: null,
    // id_item_request: [],
    item: null,
    items: [],
    expense: null,
    expenses: [],
    freightCost: null,
    freightCosts: [],
    itemDetails: {
      purchase_invoice_details: []
    },
    expenseDetails: {
      expense: []
    },
    piDetails: {
      freight_costs: []
    },
    valueCost: undefined,
    poDetails: [],
    suppDisc: "0",
    currency_rate: 1,
    expenseAmount: 0,
    totalDP: 0,
    total_qty: 0,
    discount_amount: 0,
    total_tax: 0,
    total_exclude_discount_tax: 0,
    total_discount: 0,
    total_amount: 0,
    freightAmount: 0,
    importedTransaction: 0,
    search: null,
    visible: false,
    visiblePrevTrans: false,
    visiblePurchaseReceipt: false,
    visibleFreightCost: false,
    visibleDownPayment: false,
    visibleModalCancel: false,
    inclusive_tax: false,
    netPayDay: null,
    netPayDisc: 0,
    errInvAmount: false,
    err: false,
    err_item: false,
    restore: null,
    limit: 0,
    clear: false,
    tab: "1",
    copy_trans: false
  };

  componentDidUpdate = (prevProps, prevState) => {
    const { purchaseInvoice, loading } = this.props.purchaseInvoice;
    const prevPurchaseInvoice = prevProps.purchaseInvoice.purchaseInvoice;
    const { getItemsBySupplier } = this.props;
    if (!loading && prevPurchaseInvoice !== purchaseInvoice) {
      if (purchaseInvoice) {
        getItemsBySupplier(
          purchaseInvoice.id_supplier,
          purchaseInvoice.id_location,
          moment(
            purchaseInvoice.transaction_date,
            "YYYY-MM-DD HH:mm:ss"
          ).format("YYYY-MM-DD")
        );
        purchaseInvoice.freight_costs.map(currData => {
          currData.freight_cost_items = currData.freight_cost_items.map(
            data => ({ index_no: data.index_no, id_item: data.id_item })
          );
          currData.selected_items = getSelectedItems(
            purchaseInvoice.purchase_invoice_details,
            currData.freight_cost_items
          );
        });
      }
      this.setState({
        expenseAmount: purchaseInvoice
          ? parseFloat(purchaseInvoice.total_expense)
          : 0,
        freightAmount: purchaseInvoice
          ? parseFloat(purchaseInvoice.total_freight_cost)
          : 0,
        currency_rate: purchaseInvoice
          ? parseFloat(purchaseInvoice.currency_rate)
          : 1,
        netPayDay: purchaseInvoice ? purchaseInvoice.net_payment_day : 0,
        netPayDisc: purchaseInvoice ? purchaseInvoice.payment_term.discount : 0,
        currency_rate: purchaseInvoice
          ? parseFloat(purchaseInvoice.currency_rate)
          : 1,
        items: purchaseInvoice ? purchaseInvoice.purchase_invoice_details : [],
        expenses: purchaseInvoice ? purchaseInvoice.expense : [],
        expenseDetails: {
          expense: purchaseInvoice ? purchaseInvoice.expense : []
        },
        freightCosts: purchaseInvoice ? purchaseInvoice.freight_costs : [],
        piDetails: {
          freight_costs: purchaseInvoice ? purchaseInvoice.freight_costs : []
        },
        inclusive_tax: purchaseInvoice ? purchaseInvoice.inclusive_tax : 0,
        poDetails: purchaseInvoice ? purchaseInvoice.purchase_orders : [],
        totalDP: purchaseInvoice ? purchaseInvoice.total_down_payment : 0,
        suppDisc: purchaseInvoice ? purchaseInvoice.discount : "0",
        id_location: purchaseInvoice ? purchaseInvoice.id_location : null,
        id_supplier: purchaseInvoice ? purchaseInvoice.id_supplier : null
      });
    } else if (
      !loading &&
      prevState.itemDetails.purchase_invoice_details.length === 0 &&
      this.state.limit === 0 &&
      prevPurchaseInvoice === purchaseInvoice
    ) {
      if (purchaseInvoice) {
        getItemsBySupplier(
          purchaseInvoice.id_supplier,
          purchaseInvoice.id_location,
          moment(
            purchaseInvoice.transaction_date,
            "YYYY-MM-DD HH:mm:ss"
          ).format("YYYY-MM-DD")
        );
        purchaseInvoice.freight_costs.map(currData => {
          currData.freight_cost_items = currData.freight_cost_items.map(
            data => ({ index_no: data.index_no, id_item: data.id_item })
          );
          currData.selected_items = getSelectedItems(
            purchaseInvoice.purchase_invoice_details,
            currData.freight_cost_items
          );
        });
      }
      this.setState({
        expenseAmount: purchaseInvoice
          ? parseFloat(purchaseInvoice.total_expense)
          : 0,
        freightAmount: purchaseInvoice
          ? parseFloat(purchaseInvoice.total_freight_cost)
          : 0,
        currency_rate: purchaseInvoice
          ? parseFloat(purchaseInvoice.currency_rate)
          : 1,
        netPayDay: purchaseInvoice ? purchaseInvoice.net_payment_day : 0,
        netPayDisc: purchaseInvoice ? purchaseInvoice.payment_term.discount : 0,
        currency_rate: purchaseInvoice
          ? parseFloat(purchaseInvoice.currency_rate)
          : 1,
        items: purchaseInvoice ? purchaseInvoice.purchase_invoice_details : [],
        expenses: purchaseInvoice ? purchaseInvoice.expense : [],
        expenseDetails: {
          expense: purchaseInvoice ? purchaseInvoice.expense : []
        },
        freightCosts: purchaseInvoice ? purchaseInvoice.freight_costs : [],
        piDetails: {
          freight_costs: purchaseInvoice ? purchaseInvoice.freight_costs : []
        },
        inclusive_tax: purchaseInvoice ? purchaseInvoice.inclusive_tax : 0,
        poDetails: purchaseInvoice ? purchaseInvoice.purchase_orders : [],
        totalDP: purchaseInvoice ? purchaseInvoice.total_down_payment : 0,
        suppDisc: purchaseInvoice ? purchaseInvoice.discount : "0",
        id_location: purchaseInvoice ? purchaseInvoice.id_location : null,
        id_supplier: purchaseInvoice ? purchaseInvoice.id_supplier : null,
        limit: 1
      });
    }

    // handle perubahan currency rate ketika freight cost belum mounted
    if (
      prevState.currency_rate !== this.state.currency_rate &&
      this.state.freightCosts.length > 0
    ) {
      const rate = parseFloat(this.state.currency_rate);
      let newFreights = [...this.state.freightCosts];
      newFreights.map(data => {
        data.amount = parseFloat(data.currency_amount) * rate;
      });

      this.setState({
        freightCosts: newFreights,
        piDetails: { freight_costs: newFreights }
      });
    }

    /* Ubah item list setelah dapat discount */
    if (
      prevProps.purchaseInvoice.itemDiscount !==
      this.props.purchaseInvoice.itemDiscount
    ) {
      this.setState({
        items: this.props.purchaseInvoice.itemDiscount,
        clear:
          this.props.purchaseInvoice.itemDiscount.length === 0 ? true : false
      });
    }

    /* Get Discount setelah klik copy trans */
    if (
      this.state.copy_trans &&
      this.state.itemDetails.purchase_invoice_details.length > 0 &&
      (prevProps.purchaseInvoice.itemDiscount !==
        this.props.purchaseInvoice.itemDiscount ||
        this.props.purchaseInvoice.itemDiscount.length === 0)
    ) {
      this.setState({ copy_trans: false });
      this.getDiscountItemList(false);
    }

    /* Get Discount setelah ganti supplier */
    if (
      this.state.id_supplier !== prevState.id_supplier &&
      this.state.itemDetails.purchase_invoice_details.length > 0
    ) {
      this.getDiscountItemList(true);
    }

    /* Get Discount setelah ganti location */
    if (
      this.state.id_location !== prevState.id_location &&
      this.state.itemDetails.purchase_invoice_details.length > 0
    ) {
      this.getDiscountItemList(true);
    }

    // /* Get Discount setelah ganti supplier */
    // if (
    //   this.state.id_supplier !== prevState.id_supplier &&
    //   this.state.itemDetails.purchase_invoice_details.length > 0
    // ) {
    //   // this.getDiscountItemList();

    //   /* clear item list ketika ganti supplier */
    //   this.setState({ clear: true });
    // }

    // /* Get Discount setelah ganti location */
    // if (
    //   this.state.id_location !== prevState.id_location &&
    //   this.state.itemDetails.purchase_invoice_details.length > 0
    // ) {
    //   // this.getDiscountItemList();

    //   /* clear item list ketika ganti location */
    //   this.setState({ clear: true });
    // }

    // Get Tax Base
    if (
      prevState.itemDetails.purchase_invoice_details !==
        this.state.itemDetails.purchase_invoice_details &&
      this.state.itemDetails.purchase_invoice_details.length > 0
    ) {
      const { getFieldValue } = this.props.form;
      const { setTaxBase } = this.props;

      setTaxBase(
        getTaxBase(
          this.state.itemDetails.purchase_invoice_details,
          getFieldValue("inclusive_tax")
        )
      );
    }

    // reset value freigt & cost
    if (prevState.valueCost && this.state.valueCost && this.state.freightCost) {
      this.setState({ valueCost: undefined });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      const {
        itemDetails,
        piDetails,
        expenseDetails,
        netPayDay,
        netPayDisc,
        inclusive_tax,
        id_item_request,
        currency_rate,
        expenseAmount,
        total_qty,
        discount_amount,
        total_tax,
        total_exclude_discount_tax,
        total_discount,
        total_amount,
        errInvAmount
      } = this.state;

      // const validateInvAmount = () =>
      //   parseFloat(parseFloat(total_amount).toFixed(2)) ===
      //   parseFloat(values.supplier_invoice_amount)
      //     ? false
      //     : true;

      // validasi item
      if (itemDetails.purchase_invoice_details.length <= 0) {
        return this.setState({ err_item: true });
      } else {
        this.setState({ err_item: false });
      }

      if (err) {
        this.setState({ err: true });
        return;
      }
      // else if (errInvAmount || validateInvAmount()) {
      //   this.setState({ err: true, errInvAmount: true });
      //   return;
      // }
      //   console.log(values);

      values.transaction_date = values.transaction_date.format(
        "YYYY-MM-DD HH:mm:ss"
      );
      values.payment_due_date = values.payment_due_date
        ? values.payment_due_date.format("YYYY-MM-DD HH:mm:ss")
        : null;
      // values.supplier_credit_limit = values.supplier_credit_limit
      //   ? parseFloat(values.supplier_credit_limit.replace(/\$\s?|(,*)/g, ""))
      //   : 0;

      const data = Object.assign(
        {},
        values,
        itemDetails,
        piDetails,
        expenseDetails,
        {
          net_payment_day: netPayDay,
          net_payment_discount: netPayDisc,
          inclusive_tax: inclusive_tax,
          id_item_request: id_item_request,
          currency_rate: currency_rate,
          total_qty: total_qty,
          discount_amount: discount_amount,
          total_tax: total_tax,
          total_exclude_discount_tax: total_exclude_discount_tax,
          total_discount: total_discount,
          total_amount: total_amount,
          total_expense: expenseAmount,
          total_down_payment: 0,
          pi_down_payments: []
        }
      );
      // console.log(data);
      if (!err && !errInvAmount) {
        this.setState({ err: false });
        this.props.submit(data);
      }
    });
  };

  getDiscountItemList = changed => {
    const { getFieldsValue } = this.props.form;
    const {
      itemDetails,
      piDetails,
      expenseDetails,
      netPayDay,
      netPayDisc,
      inclusive_tax,
      id_item_request,
      expenseAmount,
      total_qty,
      discount_amount,
      total_tax,
      total_exclude_discount_tax,
      total_discount,
      total_amount
    } = this.state;

    let values = getFieldsValue();

    values.transaction_date = values.transaction_date.format(
      "YYYY-MM-DD HH:mm:ss"
    );
    values.payment_due_date = values.payment_due_date
      ? values.payment_due_date.format("YYYY-MM-DD HH:mm:ss")
      : null;
    // values.supplier_credit_limit = values.supplier_credit_limit
    //   ? parseFloat(values.supplier_credit_limit.replace(/\$\s?|(,*)/g, ""))
    //   : 0;

    let modified = { purchase_invoice_details: null };
    if (changed) {
      modified.purchase_invoice_details = itemDetails.purchase_invoice_details.filter(
        item => !item.id_purchase_receipt_detail
      );
    } else {
      modified.purchase_invoice_details = [
        ...itemDetails.purchase_invoice_details
      ];
    }

    const data = Object.assign(
      {},
      values,
      modified,
      piDetails,
      expenseDetails,
      {
        net_payment_day: netPayDay,
        net_payment_discount: netPayDisc,
        inclusive_tax: inclusive_tax,
        id_item_request: id_item_request,
        total_qty: total_qty,
        discount_amount: discount_amount,
        total_tax: total_tax,
        total_exclude_discount_tax: total_exclude_discount_tax,
        total_discount: total_discount,
        total_amount: total_amount,
        total_expense: expenseAmount,
        total_down_payment: 0,
        pi_down_payments: []
      }
    );
    // console.log(data);
    this.props.getDiscounts(data);
  };

  handleModal = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleModalPurchaseReceipt = () => {
    const { visiblePurchaseReceipt } = this.state;
    visiblePurchaseReceipt
      ? this.setState({ visiblePurchaseReceipt: false })
      : this.setState({ visiblePurchaseReceipt: true });
  };

  handleModalFreightCost = () => {
    const { visibleFreightCost } = this.state;
    visibleFreightCost
      ? this.setState({ visibleFreightCost: false })
      : this.setState({ visibleFreightCost: true });
  };

  handleModalDownPayment = () => {
    const { visibleDownPayment } = this.state;
    visibleDownPayment
      ? this.setState({ visibleDownPayment: false })
      : this.setState({ visibleDownPayment: true });
  };

  handleSelectLocation = id_location => {
    const { setFieldsValue } = this.props.form;
    this.setState({
      id_location: id_location,
      id_supplier: null
      // clear: false
    });

    setFieldsValue({
      id_supplier: undefined,
      id_payment_category: undefined,
      id_payment_term: undefined,
      payment_due_date: undefined
    });
  };

  handleSelectItem = value => {
    const { items } = this.props.purchaseInvoice;
    const { getFieldValue, getFieldsValue } = this.props.form;
    const { search } = this.state;
    const { id_supplier, id_location } = getFieldsValue([
      "id_supplier",
      "id_location"
    ]);
    const { getItemsBySupplier } = this.props;
    // console.log(items.find(item => item.id_item === value));
    this.setState({
      item: items.find(item => item.id_item === value)
    });

    if (search) {
      const transDate = getFieldValue("transaction_date").format("YYYY-MM-DD");
      getItemsBySupplier(id_supplier, id_location, transDate);
      this.setState({ search: null });
    }
  };

  loop = array => {
    const { freight_costs } = this.state.piDetails;
    const { valueCost } = this.state;
    const source = valueCost
      ? [{ cost_description: valueCost }, ...freight_costs, ...array]
      : [...freight_costs, ...array];

    // filter unique description
    let modified = [];
    const map = new Map();
    for (const cost of source) {
      if (cost.cost_description && !map.has(cost.cost_description)) {
        map.set(cost.cost_description, true);
        modified.push({ cost_description: cost.cost_description });
      } else if (cost.account_name && !map.has(cost.account_name)) {
        map.set(cost.account_name, true);
        modified.push({ account_name: cost.account_name });
      }
    }

    return modified.map((data, key) => {
      let perData;
      perData = {
        text: data.account_name ? data.account_name : data.cost_description,
        value: data.account_name ? data.account_name : data.cost_description,
        key: key
        // children: data.children ? loop(data.children) : null,
        // disabled: data.children.length > 0 ? true : false
      };
      return perData;
    });
  };

  handleSelectCost = value => {
    let obj = null;

    obj = { value: value, cost_description: value };
    this.setState({ freightCost: obj });
  };

  handleSelectModal = value => {
    const { transactions } = this.props.purchaseInvoice;
    const { setFieldsValue } = this.props.form;
    let transaction = transactions.find(
      curr_data => curr_data.id_purchase_invoice === value
    );
    transaction.purchase_invoice_details.map(data => {
      data.id_department = null;
      data.id_project = null;
      data.item_note = null;
      data.id_purchase_invoice = null;
      data.id_purchase_invoice_detail = null;
      data.id_purchase_order = null;
      data.id_purchase_receipt = null;
      data.id_purchase_receipt_detail = null;
      data.purchase_order_no = null;
      data.purchase_receipt_no = null;
    });
    // console.log(transaction);
    this.setState({
      items: transaction.purchase_invoice_details,
      // itemDetails: {
      //   purchase_invoice_details: transaction.purchase_invoice_details
      // },
      inclusive_tax: transaction.inclusive_tax ? true : false,
      netPayDay: transaction.net_payment_day,
      netPayDisc: transaction.net_payment_discount,
      copy_trans: true
    });
    setFieldsValue({ inclusive_tax: transaction.inclusive_tax });
    this.handleModal();
  };

  handleSelectModalPurchaseReceipt = value => {
    const { purchaseReceipts } = this.props.purchaseInvoice;
    const { setFieldsValue, getFieldValue } = this.props.form;
    const { itemDetails, poDetails } = this.state;
    let purchaseReceipt = purchaseReceipts.find(
      curr_data => curr_data.id_purchase_receipt === value
    );

    const find = itemDetails.purchase_invoice_details.findIndex(
      currData =>
        currData.id_purchase_receipt === purchaseReceipt.id_purchase_receipt
    );
    /* Set PO No. dan PR No. ke item details */
    if (find < 0) {
      /** cek inclusive tax */
      if (
        itemDetails.purchase_invoice_details.length > 0 &&
        getFieldValue("inclusive_tax") !==
          purchaseReceipt.purchase_order.inclusive_tax
      ) {
        Modal.error({
          title: "Error",
          content: getLanguage({ id: "cannotImportDifferrentInclusiveTax" }),
          okText: getLanguage({ id: "button.ok" }),
          cancelText: getLanguage({ id: "button.cancel" })
        });
      } else {
        const inclusiveTax = purchaseReceipt.purchase_order
          ? purchaseReceipt.purchase_order.inclusive_tax
            ? purchaseReceipt.purchase_order.inclusive_tax
            : 0
          : 0;

        // filter PR yang masih bisa di PI kan
        const purchase_receipt_details = [];
        let index_no = itemDetails.purchase_invoice_details[itemDetails.purchase_invoice_details.length - 1].index_no + 1;
        purchaseReceipt.purchase_receipt_details.map(data => {
          if (
            parseFloat(data.receive_qty) - parseFloat(data.returned_qty) >
            0
          ) {
            data.index_no = index_no;
            data.purchase_receipt_no = purchaseReceipt.purchase_receipt_no;
            data.id_purchase_order = purchaseReceipt.id_purchase_order;
            data.purchase_order_no = purchaseReceipt.purchase_order_no;

            purchase_receipt_details.push(data);
            index_no++;
          }
        });

        let items = [
          ...itemDetails.purchase_invoice_details,
          ...purchase_receipt_details
        ];
        // items.map((data, index) => (data.index_no = index + 1));
        const findPO = poDetails.findIndex(
          data => data.id_purchase_order === purchaseReceipt.id_purchase_order
        );
        // cek dp sudah ada apa belum
        if (
          findPO < 0 &&
          purchaseReceipt.purchase_order
        ) {
          const dpOrder = purchaseReceipt.purchase_order;

          this.setState({
            items: items,
            poDetails: [...poDetails, dpOrder],
            id_location: purchaseReceipt.id_receipt_location,
            inclusive_tax: inclusiveTax,
            clear: false
          });
          setFieldsValue({ inclusive_tax: inclusiveTax });
        } else {
          this.setState({
            items: items,
            id_location: purchaseReceipt.id_receipt_location,
            inclusive_tax: inclusiveTax,
            clear: false
          });
          setFieldsValue({ inclusive_tax: inclusiveTax });
        }

        setFieldsValue({
          id_location: purchaseReceipt.id_receipt_location,
          id_supplier: purchaseReceipt.id_supplier,
          id_courier: purchaseReceipt.id_courier
        });
        this.handleChangeSupplier(purchaseReceipt.id_supplier, purchaseReceipt);
      }
      // console.log(purchaseReceipt);
    }

    this.handleModalPurchaseReceipt();
  };

  handleChangeTransDate = status => {
    if (!status) {
      const { setFieldsValue, getFieldValue, getFieldsValue } = this.props.form;
      const { id_supplier, id_location } = getFieldsValue([
        "id_supplier",
        "id_location"
      ]);
      const { getItemsBySupplier, getPurchaseReceipts } = this.props;
      const { paymentTerms, currencies } = this.props.purchaseInvoice;
      const paymentTerm = paymentTerms.find(
        data => data.id_payment_term === getFieldValue("id_payment_term")
      );

      let currRate = 1;
      let fiscRate = 1;
      if (getFieldValue("id_currency")) {
        const currency = currencies.find(
          data => data.id_currency === getFieldValue("id_currency")
        );
        const rate = getRate(
          currency.currency_rate,
          getFieldValue("transaction_date")
        );
        currRate = rate.currency_rate;
        fiscRate = rate.fiscal_rate;
      }

      const transDateTime = getFieldValue("transaction_date").format(
        "YYYY-MM-DD HH:mm:ss"
      );
      const transDate = getFieldValue("transaction_date").format("YYYY-MM-DD");
      const payment_due_date = paymentTerm
        ? moment(transDateTime).add(paymentTerm.net_payment_day, "d")
        : null;
      setFieldsValue({
        payment_due_date: payment_due_date,
        currency_rate: currRate ? currRate : 1,
        fiscal_rate: fiscRate ? fiscRate : 1
      });
      this.setState({ currency_rate: currRate });
      if (id_location && id_supplier) {
        getItemsBySupplier(id_supplier, id_location, transDate);
        const filterDate = moment(transDateTime).format("YYYY-MM-DD HH:mm:ss");
        const filter = 
          `id_supplier=${id_supplier}&id_receipt_location=${id_location}&transaction_date=${filterDate}`;
        getPurchaseReceipts(1, null, filter);
        if (this.state.itemDetails.purchase_invoice_details.length > 0) {
          this.getDiscountItemList(false);
        }
      }
    }
  };

  handleChangeSupplier = (id_supplier, purchaseReceipt) => {
    const { setFieldsValue, getFieldValue, getFieldsValue } = this.props.form;
    const { getItemsBySupplier } = this.props;
    const { id_location } = getFieldsValue(["id_location"]);
    const { currencies, suppliers, paymentTerms } = this.props.purchaseInvoice;
    const supplier = suppliers.find(
      data => data.id_supplier === id_supplier
    );
    const currency = currencies.find(
      data => data.id_currency === supplier.id_currency
    );
    
    let id_payment_category, id_payment_term, paymentTerm;

    if (purchaseReceipt.purchase_order) {
      const purchase_order = purchaseReceipt.purchase_order;
      id_payment_category = purchase_order.id_payment_category;
      id_payment_term = purchase_order.id_payment_term;
      paymentTerm = paymentTerms.find(
        data => data.id_payment_term === id_payment_term
      );
    } else {
      id_payment_category = supplier.id_payment_category;
      id_payment_term = supplier.id_payment_term;
      paymentTerm = paymentTerms.find(
        data => data.id_payment_term === supplier.id_payment_term
      );
    }

    const rate = getRate(
      currency.currency_rate,
      getFieldValue("transaction_date")
    );
    const currRate = rate.currency_rate;
    const fiscRate = rate.fiscal_rate;
    const transDateTime = getFieldValue("transaction_date").format(
      "YYYY-MM-DD HH:mm:ss"
    );
    const transDate = getFieldValue("transaction_date").format("YYYY-MM-DD");
    const payment_due_date = paymentTerm
      ? moment(transDateTime).add(paymentTerm.net_payment_day, "d")
      : null;
    const disc = supplier ? parseFloat(supplier.discount) * 100 + "%" : 0;
    if (id_supplier && id_location) {
      getItemsBySupplier(id_supplier, id_location, transDate);
    }
    this.setState({
      id_supplier: id_supplier,
      netPayDay: paymentTerm ? paymentTerm.net_payment_day : 0,
      netPayDisc: paymentTerm ? paymentTerm.discount : 0,
      currency_rate: currRate
      // clear: this.state.id_supplier !== id_supplier ? true : false
    });
    const e = {
      target: { value: disc }
    };
    this.handleChangeSuppDisc(e);
    setFieldsValue({
      supplier_credit_limit: supplier
        ? parseFloat(supplier.supplier_credit_limit)
            .toFixed(2)
            .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
        : 0,
      id_currency: supplier ? supplier.id_currency : null,
      currency_rate: currRate ? currRate : 1,
      fiscal_rate: fiscRate ? fiscRate : 1,
      id_payment_category: id_payment_category,
      id_payment_term: id_payment_term,
      payment_due_date: payment_due_date,
      discount: disc
    });
  };

  handleChangePaymentTerm = id_payment_term => {
    const { setFieldsValue, getFieldValue } = this.props.form;
    const { paymentTerms } = this.props.purchaseInvoice;
    const paymentTerm = paymentTerms.find(
      data => data.id_payment_term === id_payment_term
    );
    const transDate = getFieldValue("transaction_date").format(
      "YYYY-MM-DD HH:mm:ss"
    );
    const payment_due_date = paymentTerm
      ? moment(transDate).add(paymentTerm.net_payment_day, "d")
      : null;
    this.setState({
      netPayDay: paymentTerm.net_payment_day,
      netPayDisc: paymentTerm.discount
    });
    setFieldsValue({ payment_due_date: payment_due_date });
  };

  handleInclusiveTax = e => {
    /** cek inclusive tax item list */
    const { poDetails } = this.state;
    const { setFieldsValue, getFieldValue } = this.props.form;

    let inclusive_tax = getFieldValue("inclusive_tax");
    // let find = -1;
    const find = poDetails.findIndex(data => data.inclusive_tax !== e.target.value)
    // poDetails.map((data, key) => {
    //   if (data.inclusive_tax !== e.target.value) {
    //     return (find = key);
    //   }
    // });

    if (poDetails.length > 0 && find >= 0) {
      Modal.error({
        title: "Error",
        content: getLanguage({ id: "cannotChangeInclusiveTaxPurchaseOrder" }),
        onOk: () => {
          setFieldsValue({ inclusive_tax: inclusive_tax });
          this.setState({ inclusive_tax: inclusive_tax });
        },
        okText: getLanguage({ id: "button.ok" }),
        cancelText: getLanguage({ id: "button.cancel" })
      });
    } else {
      this.setState({ inclusive_tax: e.target.value });
    }
  };

  handleSearchItem = value => {
    const { getItemsBySupplier } = this.props;
    const { getFieldValue, getFieldsValue } = this.props.form;
    const { id_supplier, id_location } = getFieldsValue([
      "id_supplier",
      "id_location"
    ]);
    // console.log(this.props.purchaseInvoice);
    this.setState({ search: value });
    if (id_supplier && id_location) {
      const transDate = getFieldValue("transaction_date").format("YYYY-MM-DD");
      getItemsBySupplier(id_supplier, id_location, transDate, value);
    }
  };

  handleChangeSuppDisc = e => {
    const {
      itemDetails,
      inclusive_tax,
      expenseAmount,
      freightAmount
    } = this.state;
    const { freight_costs } = this.state.piDetails;
    const { purchase_invoice_details } = itemDetails;
    let value = "0";
    if (e.target.value && e.target.value.slice(-1) === "%") {
      value = `${parseDiscount(e.target.value)}%`;
    } else {
      value = parseDiscount(e.target.value);
    }
    const totalCostTax = getTotalCostTax(freight_costs);

    this.setState({
      suppDisc: value,
      discount_amount: getSupplierDiscount(purchase_invoice_details, value),
      total_tax: getTotalTax(
        purchase_invoice_details,
        value,
        inclusive_tax,
        totalCostTax
      ),
      total_exclude_discount_tax: getExlDiscTax(
        purchase_invoice_details,
        inclusive_tax,
        value,
        expenseAmount
      ),
      total_discount: getTotalDiscount(
        purchase_invoice_details,
        value,
        inclusive_tax
      ),
      total_amount: getTotalAmount(
        purchase_invoice_details,
        value,
        inclusive_tax,
        expenseAmount,
        freightAmount,
        totalCostTax
      )
    });
  };

  handleSaveItemList = (items, total_qty, sync, importedTransaction = 0) => {
    const {
      suppDisc,
      inclusive_tax,
      expenseAmount,
      freightAmount
    } = this.state;
    const { freight_costs } = this.state.piDetails;
    let newFreightCosts = [];
    freight_costs.map(data => {
      const selected_items = getSelectedItems(items, data.freight_cost_items);
      newFreightCosts.push(
        Object.assign({}, data, { selected_items: selected_items })
      );
    });
    const totalCostTax = getTotalCostTax(newFreightCosts);
    const setStateData = {
      itemDetails: { purchase_invoice_details: items },
      piDetails: { freight_costs: newFreightCosts },
      total_qty: total_qty,
      discount_amount: getSupplierDiscount(items, suppDisc),
      total_tax:
        items.length > 0
          ? getTotalTax(items, suppDisc, inclusive_tax, totalCostTax)
          : 0,
      total_exclude_discount_tax:
        items.length > 0
          ? getExlDiscTax(items, inclusive_tax, suppDisc, expenseAmount)
          : 0,
      total_discount: getTotalDiscount(items, suppDisc, inclusive_tax),
      total_amount: getTotalAmount(
        items,
        suppDisc,
        inclusive_tax,
        expenseAmount,
        freightAmount,
        totalCostTax
      ),
      importedTransaction,
      clear: false
    };

    if (sync) {
      this.syncPurchaseOrderDetail(items, setStateData);
    } else {
      this.setState(setStateData);
    }
  };

  syncPurchaseOrderDetail = (items, stateData) => {
    const { poDetails } = this.state;
    let arrPO = [];
    items.map(data => arrPO.push(data.id_purchase_order));
    let newPoDetails = poDetails.filter(data =>
      arrPO.includes(data.id_purchase_order)
    );
    const setStateData = Object.assign({}, stateData, {
      poDetails: newPoDetails
    });

    this.setState(setStateData);
  };

  handleSaveFreightCostList = (
    freightCosts,
    total_amount,
    total_base_amount
  ) => {
    const { inclusive_tax, suppDisc, expenseAmount } = this.state;
    const totalCostTax = getTotalCostTax(freightCosts);
    const items = this.state.itemDetails.purchase_invoice_details;

    this.setState({
      piDetails: { freight_costs: freightCosts },
      freightAmount: total_amount,
      total_tax: getTotalTax(items, suppDisc, inclusive_tax, totalCostTax),
      total_amount: getTotalAmount(
        items,
        suppDisc,
        inclusive_tax,
        expenseAmount,
        total_amount,
        totalCostTax
      )
    });
  };

  validateInvAmount = value => {
    const { total_amount } = this.state;
    if (parseFloat(parseFloat(total_amount).toFixed(2)) !== parseFloat(value)) {
      this.setState({ errInvAmount: true });
    } else {
      this.setState({ errInvAmount: false });
    }
  };

  clearItem = (items, total_qty) => {
    const {
      suppDisc,
      inclusive_tax,
      expenseAmount,
      freightAmount
    } = this.state;
    const { freight_costs } = this.state.piDetails;
    let newFreightCosts = [];
    freight_costs.map(data => {
      const selected_items = getSelectedItems(items, data.freight_cost_items);
      newFreightCosts.push(
        Object.assign({}, data, { selected_items: selected_items })
      );
    });
    const totalCostTax = getTotalCostTax(newFreightCosts);

    this.setState({
      item: null,
      itemDetails: { purchase_invoice_details: items },
      piDetails: { freight_costs: newFreightCosts },
      total_qty: total_qty,
      discount_amount: getSupplierDiscount(items, suppDisc),
      total_tax:
        items.length > 0
          ? getTotalTax(items, suppDisc, inclusive_tax, totalCostTax)
          : 0,
      total_exclude_discount_tax:
        items.length > 0
          ? getExlDiscTax(items, inclusive_tax, suppDisc, expenseAmount)
          : 0,
      total_discount: getTotalDiscount(items, suppDisc, inclusive_tax),
      total_amount: getTotalAmount(
        items,
        suppDisc,
        inclusive_tax,
        expenseAmount,
        freightAmount,
        totalCostTax
      )
    });
  };

  clearItems = (items, total_qty, importedTransaction) => {
    const {
      suppDisc,
      inclusive_tax,
      expenseAmount,
      freightAmount
    } = this.state;
    const { freight_costs } = this.state.piDetails;
    let newFreightCosts = [];
    freight_costs.map(data => {
      const selected_items = getSelectedItems(items, data.freight_cost_items);
      newFreightCosts.push(
        Object.assign({}, data, { selected_items: selected_items })
      );
    });
    const totalCostTax = getTotalCostTax(newFreightCosts);

    this.setState({
      items: [],
      itemDetails: { purchase_invoice_details: items },
      piDetails: { freight_costs: newFreightCosts },
      total_qty: total_qty,
      discount_amount: getSupplierDiscount(items, suppDisc),
      total_tax:
        items.length > 0
          ? getTotalTax(items, suppDisc, inclusive_tax, totalCostTax)
          : 0,
      total_exclude_discount_tax:
        items.length > 0
          ? getExlDiscTax(items, inclusive_tax, suppDisc, expenseAmount)
          : 0,
      total_discount: getTotalDiscount(items, suppDisc, inclusive_tax),
      total_amount: getTotalAmount(
        items,
        suppDisc,
        inclusive_tax,
        expenseAmount,
        freightAmount,
        totalCostTax
      ),
      importedTransaction,
      clear: false
    });
  };

  clearFreightCost = (freightCosts, total_amount, total_base_amount) => {
    const { inclusive_tax, suppDisc, expenseAmount } = this.state;
    const totalCostTax = getTotalCostTax(freightCosts);
    const items = this.state.itemDetails.purchase_invoice_details;

    this.setState({
      freightCost: null,
      piDetails: { freight_costs: freightCosts },
      freightAmount: total_amount,
      total_tax: getTotalTax(items, suppDisc, inclusive_tax, totalCostTax),
      total_amount: getTotalAmount(
        items,
        suppDisc,
        inclusive_tax,
        expenseAmount,
        total_amount,
        totalCostTax
      )
    });
  };

  clearFreightCosts = (freightCosts, total_amount, total_base_amount) => {
    const { inclusive_tax, suppDisc, expenseAmount } = this.state;
    const totalCostTax = getTotalCostTax(freightCosts);
    const items = this.state.itemDetails.purchase_invoice_details;

    this.setState({
      freightCosts: [],
      piDetails: { freight_costs: freightCosts },
      freightAmount: total_amount,
      total_tax: getTotalTax(items, suppDisc, inclusive_tax, totalCostTax),
      total_amount: getTotalAmount(
        items,
        suppDisc,
        inclusive_tax,
        expenseAmount,
        total_amount,
        totalCostTax
      )
    });
  };

  handleModalCancel = () => {
    this.setState({
      visibleModalCancel: !this.state.visibleModalCancel
    });
  };

  render() {
    // console.log(this.state.itemDetails);
    const {
      importedTransaction,
      search,
      visible,
      visiblePurchaseReceipt
      // visibleDownPayment
    } = this.state;
    const {
      getFieldDecorator,
      getFieldError,
      setFieldsValue,
      getFieldValue
    } = this.props.form;

    const { purchaseInvoice } = this.props;
    const {
      baseCurrency,
      locations,
      suppliers,
      currencies,
      accounts,
      paymentCategories,
      paymentTerms,
      projects,
      departments,
      couriers,
      items,
      taxes,
      tax_bases
    } = purchaseInvoice ? purchaseInvoice : [];
    const { user } = this.props;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    const {
      loading,
      loadingSubmit,
      loadingItems,
      loadingTransactions,
      loadingPurchaseReceipts,
      loadingForeign,
      loadingTable
    } = purchaseInvoice ? purchaseInvoice : [];
    const data = purchaseInvoice.purchaseInvoice
      ? purchaseInvoice.purchaseInvoice
      : [];

    // const used = data.is_used;
    const error = purchaseInvoice
      ? purchaseInvoice.errorField
        ? purchaseInvoice.errorField
        : []
      : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const paymentStatus = () => {
      let className = null;
      let display = null;

      switch (data.payment_status) {
        case 0:
          className = "new-payment inside-form";
          display = getLanguage({ id: "new" });
          break;
        case 1:
          className = "billed-payment inside-form";
          display = getLanguage({ id: "billed" });
          break;
        case 2:
          className = "paid-payment inside-form";
          display = getLanguage({ id: "paid" });
          break;
        default:
          className = "new-payment inside-form";
          display = getLanguage({ id: "new" });
          break;
      }
      return <StatusTag className={className}>{display}</StatusTag>;
      // return { className: className, display: display };
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalTransaction
          visible={visible}
          purchaseInvoice={purchaseInvoice}
          handleModal={this.handleModal}
          handleSelectModal={this.handleSelectModal}
          id_location={getFieldValue("id_location")}
          id_supplier={getFieldValue("id_supplier")}
        />

        <ModalPurchaseReceipt
          visible={visiblePurchaseReceipt}
          purchaseInvoice={purchaseInvoice}
          handleModal={this.handleModalPurchaseReceipt}
          handleSelectModal={this.handleSelectModalPurchaseReceipt}
          id_location={getFieldValue("id_location")}
          id_supplier={getFieldValue("id_supplier")}
          transaction_date={getFieldValue("transaction_date")
            ? moment(getFieldValue("transaction_date")).format("YYYY-MM-DD HH:mm:ss")
            : moment().format("YYYY-MM-DD HH:mm:ss")
          }
        />

        {/* <ModalDownPayment
          visible={visibleDownPayment}
          purchaseOrders={this.state.poDetails}
          handleModal={this.handleModalDownPayment}
          loading={purchaseInvoice.loadingPurchaseReceipts}
          totalDP={this.state.totalDP}
        /> */}

        <ModalCancel
          visible={this.state.visibleModalCancel}
          handleModal={this.handleModalCancel}
          submit={this.props.handleSubmitCancel}
          loading={loadingSubmit}
        />

        <Row style={{ margin: "12px 0px" }}>
          <Col span={12}>
            <Steps
              size="small"
              progressDot={customDot}
              current={data.status - 1}
            >
              <Step title={getLanguage({ id: "pending" })} />
              <Step title={getLanguage({ id: "processed" })} />
              <Step title={getLanguage({ id: "cancelled" })} />
            </Steps>
          </Col>
        </Row>

        <Tabs
          defaultActiveKey="1"
          onTabClick={key => this.setState({ tab: key })}
        >
          <TabPane
            tab={<IntlMessages id="purchaseInvoices.tabs.detail" />}
            key="1"
          >
            <Row>
              <Col span={12} style={{ paddingRight: "15px" }}>
                <InputGroup>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={
                        <IntlMessages id="purchaseInvoices.purchaseInvoiceNo" />
                      }
                    >
                      {getFieldDecorator("purchase_invoice_no", {
                        initialValue: data.purchase_invoice_no
                      })(<Input disabled={true} />)}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={
                        <Tooltip
                          placement="top"
                          title={
                            <IntlMessages id="transactionDateHelp" />
                          }
                        >
                          <IntlMessages id="purchaseInvoices.transactionDate" />
                          <Icon
                            style={{ marginLeft: "5px" }}
                            type="question-circle"
                            theme="filled"
                          />
                        </Tooltip>
                      }
                      style={{ marginBottom: 0, paddingBottom: 0 }}
                    >
                      {getFieldDecorator("transaction_date", {
                        initialValue: moment(
                          data.transaction_date,
                          "YYYY-MM-DD HH:mm:ss"
                        )
                      })(
                        <DatePicker
                          style={{ width: "100%" }}
                          showTime
                          format="DD MMMM YYYY HH:mm:ss"
                          // onChange={this.handleChangeTransDate}
                          onOpenChange={this.handleChangeTransDate}
                          disabled={
                            !permissions.includes("Can Back Date") ||
                            importedTransaction > 0 ||
                            data.status !== 1 ||
                            data.transaction_type !== 1
                          }
                          allowClear={false}
                          disabledDate={e => e.valueOf() > moment()}
                        />
                      )}
                    </Form.Item>
                  </Col>
                </InputGroup>

                <Col span={24}>
                  <InputGroup>
                    <Col span={16}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <Tooltip
                            placement="top"
                            title={
                              <IntlMessages id="purchaseInvoices.location.help" />
                            }
                          >
                            <IntlMessages id="purchaseInvoices.location" />
                            <Icon
                              style={{ marginLeft: "5px" }}
                              type="question-circle"
                              theme="filled"
                            />
                          </Tooltip>
                        }
                      >
                        {getFieldDecorator("id_location", {
                          initialValue: data.id_location,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            showSearch
                            style={{ width: "100%" }}
                            placeholder={
                              <IntlMessages id="purchaseInvoices.location" />
                            }
                            loading={loadingForeign}
                            onSelect={this.handleSelectLocation}
                            disabled={data.status !== 1 || data.transaction_type !== 1}
                            optionFilterProp="children"
                            filterOption={true}
                          >
                            {locations.map((data, key) => (
                              <Option key={key} value={data.id_location}>
                                {`${data.location_code} (${
                                  data.location_name
                                })`}
                              </Option>
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={8}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <Tooltip
                            placement="top"
                            title={
                              <IntlMessages id="purchaseInvoices.importPR.help" />
                            }
                          >
                            <IntlMessages id="purchaseInvoices.importPR" />
                            <Icon
                              style={{ marginLeft: "5px" }}
                              type="question-circle"
                              theme="filled"
                            />
                          </Tooltip>
                        }
                      >
                        <Button
                          icon="select"
                          loading={loadingPurchaseReceipts}
                          disabled={
                            loadingPurchaseReceipts ||
                            !getFieldValue("id_location") ||
                            !getFieldValue("id_supplier") ||
                            data.status !== 1 ||
                            data.transaction_type !== 1
                          }
                          onClick={this.handleModalPurchaseReceipt}
                          style={{ height: "31px", width: "100%" }}
                          // shape="circle"
                        >
                          <IntlMessages id="button.import" />
                        </Button>
                      </Form.Item>
                    </Col>
                  </InputGroup>
                </Col>
              </Col>
              <Col span={12} style={{ paddingLeft: "15px" }}>
                <Form.Item
                  {...formTextArea}
                  label={<IntlMessages id="purchaseInvoices.note" />}
                >
                  {getFieldDecorator("note", {
                    initialValue: data.note
                  })(
                    <TextArea
                      disabled={data.status !== 1 || data.transaction_type !== 1}
                      autosize={{ minRows: 4 }}
                    />
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col span={12} style={{ paddingRight: "15px" }}>
                <InputGroup>
                  <Col span={16}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="purchaseInvoices.supplier" />}
                    >
                      {getFieldDecorator("id_supplier", {
                        initialValue: data.id_supplier,
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <Select
                          showSearch
                          style={{ width: "100%" }}
                          placeholder={
                            <IntlMessages id="purchaseOrders.supplier" />
                          }
                          loading={loadingForeign}
                          onSelect={this.handleChangeSupplier}
                          notFoundContent={
                            getFieldValue("id_location") ? (
                              <Empty
                                description={<IntlMessages id="noData" />}
                              />
                            ) : (
                              <Empty
                                description={<IntlMessages id="noLocation" />}
                              />
                            )
                          }
                          disabled={data.status !== 1 || data.transaction_type !== 1}
                          optionFilterProp="children"
                          filterOption={true}
                        >
                          {suppliers.map((data, key) =>
                            data.id_location ===
                            getFieldValue("id_location") ? (
                              <Option key={key} value={data.id_supplier}>
                                {`${data.supplier_code} (${
                                  data.supplier_name
                                })`}
                              </Option>
                            ) : null
                          )}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={8}>
                    {data.status === 1 ? (
                      <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="purchaseInvoices.creditLimit" />}
                      >
                        {getFieldDecorator("supplier_credit_limit", {
                          initialValue: parseFloat(data.supplier_credit_limit)
                            .toFixed(2)
                            .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        })(<Input disabled={true} />)}
                      </Form.Item>
                    ) : data.status !== 3 && data.transaction_type === 1 ? (
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <IntlMessages id="purchaseInvoices.paymentStatus" />
                        }
                      >
                        {paymentStatus()}
                        {/* <StatusTag className="billed-payment">Billed</StatusTag> */}
                      </Form.Item>
                    ) : (
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="purchaseInvoices.currency" />}
                      >
                        {getFieldDecorator("id_currency", {
                          initialValue: data.id_currency
                        })(
                          <Select
                            showSearch
                            style={{ width: "100%" }}
                            // placeholder={
                            //   <IntlMessages id="purchaseInvoices.currency" />
                            // }
                            loading={loadingForeign}
                            disabled={true}
                            optionFilterProp="children"
                            filterOption={true}
                          >
                            {currencies.map((data, key) => (
                              <Option key={key} value={data.id_currency}>
                                {data.currency_code}
                              </Option>
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    )}
                  </Col>
                </InputGroup>
              </Col>
              <Col span={12} style={{ paddingLeft: "15px" }}>
                <InputGroup>
                  {data.status === 1 ? (
                    <Col span={6}>
                      {data.transaction_type !== 1 ? null : (
                        <Form.Item
                          {...formItemLayout}
                          label={
                            <IntlMessages id="purchaseInvoices.paymentStatus" />
                          }
                        >
                          {paymentStatus()}
                          {/* <StatusTag className="billed-payment">Billed</StatusTag> */}
                        </Form.Item>
                      )}
                    </Col>
                  ) : null}
                  {data.status !== 3 ? (
                    <Col span={6}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="purchaseInvoices.currency" />}
                      >
                        {getFieldDecorator("id_currency", {
                          initialValue: data.id_currency
                        })(
                          <Select
                            showSearch
                            style={{ width: "100%" }}
                            // placeholder={
                            //   <IntlMessages id="purchaseInvoices.currency" />
                            // }
                            loading={loadingForeign}
                            disabled={true}
                            optionFilterProp="children"
                            filterOption={true}
                          >
                            {currencies.map((data, key) => (
                              <Option key={key} value={data.id_currency}>
                                {data.currency_code}
                              </Option>
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                  ) : null}
                  <Col span={6}>
                    <Form.Item
                      {...formItemLayout}
                      label={
                        <Tooltip
                          placement="top"
                          title={
                            <IntlMessages id="purchaseInvoices.currencyRate.help" />
                          }
                        >
                          <IntlMessages id="purchaseInvoices.currencyRate" />
                          <Icon
                            style={{ marginLeft: "5px" }}
                            type="question-circle"
                            theme="filled"
                          />
                        </Tooltip>
                      }
                    >
                      {getFieldDecorator("currency_rate", {
                        initialValue: data.currency_rate
                      })(
                        <InputNumber
                          placeholder={getLanguage({ id: "purchaseInvoices.currencyRate" })}
                          formatter={value =>
                            `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                          }
                          parser={value => value.replace(/\$\s?|(,*)/g, "")}
                          min={0}
                          style={{ width: "100%" }}
                          disabled={
                            getFieldValue("id_currency") &&
                            currencies.length > 0
                              ? purchaseInvoice.currencies.find(
                                  cur =>
                                    cur.id_currency ===
                                    getFieldValue("id_currency")
                                ).is_base === 1 || data.status !== 1 || data.transaction_type !== 1
                                ? true
                                : false
                              : false
                          }
                          onBlur={() => {
                            this.getDiscountItemList(false);
                            this.setState({
                              currency_rate: getFieldValue("currency_rate")
                            });
                          }}
                        />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={6}>
                    <Form.Item
                      {...formItemLayout}
                      label={
                        <Tooltip
                          placement="top"
                          title={
                            <IntlMessages id="purchaseInvoices.fiscalRate.help" />
                          }
                        >
                          <IntlMessages id="purchaseInvoices.fiscalRate" />
                          <Icon
                            style={{ marginLeft: "5px" }}
                            type="question-circle"
                            theme="filled"
                          />
                        </Tooltip>
                      }
                    >
                      {getFieldDecorator("fiscal_rate", {
                        initialValue: data.fiscal_rate
                      })(
                        <InputNumber
                          placeholder={getLanguage({ id: "purchaseInvoices.fiscalRate" })}
                          formatter={value =>
                            `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                          }
                          parser={value => value.replace(/\$\s?|(,*)/g, "")}
                          min={0}
                          style={{ width: "100%" }}
                          disabled={
                            getFieldValue("id_currency") &&
                            currencies.length > 0
                              ? purchaseInvoice.currencies.find(
                                  cur =>
                                    cur.id_currency ===
                                    getFieldValue("id_currency")
                                ).is_base === 1 || data.status !== 1 || data.transaction_type !== 1
                                ? true
                                : false
                              : false
                          }
                        />
                      )}
                    </Form.Item>
                  </Col>
                </InputGroup>
              </Col>
            </Row>

            <Divider orientation="left" style={{ fontSize: 14 }}>
              <IntlMessages id="purchaseInvoices.subtitle.itemsExpenses" />
            </Divider>

            <Tabs defaultActiveKey="1">
              <TabPane
                tab={<IntlMessages id="purchaseInvoices.tabs.item" />}
                key={1}
              >
                <Row style={{ marginBottom: "25px" }}>
                  <Col span={24}>
                    <Form.Item
                      {...formItemLayout}
                      validateStatus={
                        error.purchase_invoice_details || this.state.err_item 
                          ? "error" 
                          : null
                      }
                      help={
                        error.purchase_invoice_details 
                          ? error.purchase_invoice_details 
                          : this.state.err_item
                            ? <IntlMessages id="itemDataEmpty" />
                            : null
                      }
                      extra={<IntlMessages id="purchaseInvoices.item.help" />}
                    >
                      <Select
                        value={search ? search : []}
                        showSearch
                        allowClear
                        // optionFilterProp="children"
                        filterOption={false}
                        placeholder={
                          <IntlMessages id="purchaseInvoices.item.placeholder" />
                        }
                        loading={loadingItems}
                        notFoundContent={
                          loadingItems ? (
                            <Spin size="small" />
                          ) : getFieldValue("id_supplier") &&
                            getFieldValue("id_location") ? (
                            <Empty description={<IntlMessages id="noData" />} />
                          ) : (
                            <Empty
                              description={
                                <IntlMessages id="purchaseInvoices.item.noSelection" />
                              }
                            />
                          )
                        }
                        onSelect={this.handleSelectItem}
                        onSearch={this.handleSearchItem}
                        disabled={data.status !== 1 || data.transaction_type !== 1}
                        optionFilterProp="children"
                        filterOption={true}
                        value={undefined}
                      >
                        {items.map(data =>
                          getFieldValue("id_supplier") ? (
                            <Option key={data.id_item} value={data.id_item}>
                              {data.item_code + " - " + data.item_name}
                            </Option>
                          ) : null
                        )}
                      </Select>
                    </Form.Item>
                  </Col>
                  <Col span={24}>
                    <EditableTable
                      item={this.state.item}
                      items={this.state.items}
                      suppDisc={this.state.suppDisc}
                      currency_rate={getFieldValue("currency_rate")}
                      taxes={taxes}
                      departments={departments}
                      projects={projects}
                      inclusive_tax={this.state.inclusive_tax}
                      freightCosts={this.state.piDetails.freight_costs}
                      status={data.status}
                      handleSaveItemList={this.handleSaveItemList.bind(this)}
                      clearItem={this.clearItem}
                      clearItems={this.clearItems}
                      loadingTransactions={loadingTransactions}
                      loadingItems={loadingItems}
                      loadingTable={loadingTable}
                      clear={this.state.clear}
                      permissions={permissions}
                    />
                  </Col>
                  <Col span={12}>
                    <Col
                      span={10}
                      style={{ marginTop: "15px", textAlign: "left" }}
                    >
                      <strong>
                        <IntlMessages id="purchaseInvoices.totalQty" />
                      </strong>
                      <strong>
                        <IntlMessages id="bsUnt">
                          {txt => ` (${txt})`}
                        </IntlMessages>
                      </strong>
                    </Col>
                    <Col span={13} offset={1} style={{ marginTop: "15px" }}>
                      <strong>
                        <NumberFormat value={this.state.total_qty} />
                      </strong>
                    </Col>
                  </Col>
                </Row>
              </TabPane>
              <TabPane
                tab={<IntlMessages id="purchaseInvoices.tabs.freightCost" />}
                key={3}
              >
                <Row>
                  <Col span={24}>
                    <Form.Item
                      {...formItemLayout}
                      extra={
                        <IntlMessages id="purchaseInvoices.description.help" />
                      }
                    >
                      <AutoComplete
                        dataSource={this.loop(accounts)}
                        allowClear={true}
                        style={{ width: "100%" }}
                        onSelect={this.handleSelectCost}
                        onChange={value => this.setState({ valueCost: value })}
                        filterOption={(inputValue, option) =>
                          option.props.children
                            .toUpperCase()
                            .indexOf(inputValue.toUpperCase()) !== -1
                        }
                        placeholder={
                          <IntlMessages id="purchaseInvoices.description.placeholder" />
                        }
                        value={this.state.valueCost}
                        disabled={data.status !== 1 || data.transaction_type !== 1}
                      />
                    </Form.Item>
                  </Col>
                </Row>
                <Row style={{ marginBottom: "40px" }}>
                  <Col span={24}>
                    <EditableTableFreightCost
                      freightCost={this.state.freightCost}
                      freightCosts={this.state.freightCosts}
                      baseCurrency={baseCurrency}
                      currency_rate={this.state.currency_rate}
                      departments={departments}
                      projects={projects}
                      items={this.state.itemDetails.purchase_invoice_details}
                      status={data.status}
                      handleSaveFreightCostList={this.handleSaveFreightCostList}
                      clearFreightCost={this.clearFreightCost}
                      clearFreightCosts={this.clearFreightCosts}
                    />
                  </Col>
                </Row>
              </TabPane>
            </Tabs>

            <Row>
              <Col span={12}>
                {/* <Row
                  type="flex"
                  justify="start"
                  style={{ marginBottom: "6px", marginTop: "5px" }}
                >
                  <Col span={12}>
                    <IntlMessages id="purchaseInvoices.poDownPayment" />
                  </Col>
                  <Col span={11} offset={1}>
                    <span style={{ marginRight: "5px" }}>
                      {parseFloat(this.state.totalDP)
                        .toFixed(2)
                        .replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                    </span>
                    <Button size="small" onClick={this.handleModalDownPayment}>
                      <IntlMessages id="button.view" />
                    </Button>
                  </Col>
                </Row> */}
                <Row
                  type="flex"
                  justify="start"
                  style={{ marginBottom: "6px" }}
                >
                  <Col span={12} style={{ marginTop: "5px" }}>
                    <label className="ant-form-item-required">
                      <IntlMessages id="purchaseInvoices.supplierInvoiceAmount" />
                    </label>
                  </Col>
                  <Col span={11} offset={1}>
                    {getFieldDecorator("supplier_invoice_amount", {
                      initialValue: data.supplier_invoice_amount,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <InputNumber
                        placeholder={getLanguage({ id: "purchaseInvoices.supplierInvoiceAmount" })}
                        formatter={value =>
                          `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={value => value.replace(/\$\s?|(,*)/g, "")}
                        size="small"
                        style={{
                          marginTop: "3px",
                          width: "100%",
                          borderColor: getFieldError("supplier_invoice_amount")
                            ? "#f5222d"
                            : "#d9d9d9"
                        }}
                        // onChange={this.validateInvAmount}
                        precision={2}
                        disabled={data.status !== 1 || data.transaction_type !== 1}
                      />
                    )}
                    {getFieldError("supplier_invoice_amount") ? (
                      <span style={{ color: "#f5222d" }}>
                        <IntlMessages id="required" />
                      </span>
                    ) : null}
                  </Col>
                </Row>
                <Row
                  type="flex"
                  justify="start"
                  style={{ marginBottom: "35px" }}
                >
                  <Col span={12} style={{ marginTop: "5px" }}>
                    <label className="ant-form-item-required">
                      <IntlMessages id="purchaseInvoices.taxInvoiceNo" />
                    </label>
                  </Col>
                  <Col span={11} offset={1}>
                    {getFieldDecorator("tax_invoice_no", {
                      initialValue: data.tax_invoice_no,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Input
                        placeholder={getLanguage({ id: "purchaseInvoices.taxInvoiceNo" })}
                        size="small"
                        style={{
                          marginTop: "3px",
                          borderColor: getFieldError("tax_invoice_no")
                            ? "#f5222d"
                            : "#d9d9d9"
                        }}
                        disabled={data.status !== 1 || data.transaction_type !== 1}
                      />
                    )}
                    {getFieldError("tax_invoice_no") ? (
                      <span style={{ color: "#f5222d" }}>
                        <IntlMessages id="required" />
                      </span>
                    ) : null}
                  </Col>
                </Row>

                {tax_bases.length > 0 ? (
                  <Row style={{ marginBottom: "6px" }}>
                    <Col span={24}>
                      <strong>
                        <IntlMessages id="taxBase" />
                      </strong>
                    </Col>
                  </Row>
                ) : null}

                {tax_bases.map((data, key) => {
                  return (
                    <Row type="flex" key={key} style={{ marginBottom: "6px" }}>
                      <Col span={12} style={{ marginTop: "5px" }}>
                        <span>{data.tax_name}</span>
                      </Col>
                      <Col span={11} offset={1} style={{ marginTop: "5px" }}>
                        <span>
                          <NumberFormat value={data.tax_base} />
                        </span>
                      </Col>
                    </Row>
                  );
                })}
              </Col>
              <Col span={12} style={{ paddingLeft: "15px" }}>
                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.exlDiscountAndTax" />
                  </Col>
                  <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                    <span>
                      <NumberFormat value={this.state.total_exclude_discount_tax} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={10}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.discount" />
                  </Col>
                  <Col span={4} offset={1}>
                    <Tooltip
                      placement="bottom"
                      title={
                        <IntlMessages id="purchaseInvoices.discount.help" />
                      }
                    >
                      {getFieldDecorator("discount", {
                        initialValue: data.discount
                      })(
                        <Input
                          size="small"
                          disabled={
                            !permissions.includes(
                              "Edit Discount Purchase Invoice"
                            ) || data.status !== 1 ||
                            data.transaction_type !== 1
                          }
                          onChange={this.handleChangeSuppDisc}
                          onBlur={() =>
                            setFieldsValue({ discount: this.state.suppDisc })
                          }
                          style={{ 
                            marginTop: "3px",
                            borderColor: this.state.total_exclude_discount_tax - this.state.discount_amount < 0
                              ? "#f5222d"
                              : "#d9d9d9"
                          }}
                        />
                      )}
                    </Tooltip>
                  </Col>
                  <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                    <span>
                      <NumberFormat value={this.state.discount_amount} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.totalDiscount" />
                  </Col>
                  <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                    <span>
                      <NumberFormat value={this.state.total_discount} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.totalTax" />
                  </Col>
                  <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                    <span>
                      <NumberFormat value={this.state.total_tax} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.freightCost" />
                  </Col>
                  <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                    <span>
                      <NumberFormat value={this.state.freightAmount} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <strong>
                      <IntlMessages id="total" />
                    </strong>
                  </Col>
                  <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                    <span>
                      {data.transaction_type !== 1 
                        ? (<NumberFormat value={data.total_amount} />)
                        : (<NumberFormat value={this.state.total_amount} />)}
                    </span>
                  </Col>
                </Row>
              </Col>
            </Row>
          </TabPane>
          <TabPane
            tab={<IntlMessages id="purchaseInvoices.tabs.payment" />}
            key="2"
          >
            <Row>
              <Col span={12} style={{ paddingRight: "15px" }}>
                <InputGroup>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={
                        <IntlMessages id="purchaseInvoices.supplierInvoiceNo" />
                      }
                    >
                      {getFieldDecorator("supplier_invoice_no", {
                        initialValue: data.supplier_invoice_no,
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                          <Input
                            placeholder={getLanguage({ id: "purchaseInvoices.supplierInvoiceNo" })}
                            disabled={data.status !== 1 || data.transaction_type !== 1}
                          />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="purchaseInvoices.dueDate" />}
                    >
                      {getFieldDecorator("payment_due_date", {
                        initialValue: moment(
                          data.payment_due_date,
                          "YYYY-MM-DD"
                        )
                      })(
                        <DatePicker
                          style={{ width: "100%" }}
                          showTime
                          format="DD MMMM YYYY HH:mm:ss"
                          disabled={true}
                          allowClear={false}
                          disabledDate={e => e.valueOf() > moment()}
                        />
                      )}
                    </Form.Item>
                  </Col>
                </InputGroup>
                <Col span={6}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="purchaseInvoices.inclusiveTax" />}
                  >
                    {getFieldDecorator("inclusive_tax", {
                      initialValue: data.inclusive_tax
                    })(
                      <Radio.Group
                        onChange={this.handleInclusiveTax}
                        buttonStyle="solid"
                        disabled={data.status !== 1 || data.transaction_type !== 1}
                      >
                        <Radio.Button value={0}>
                          <IntlMessages id="no" />
                        </Radio.Button>
                        <Radio.Button value={1}>
                          <IntlMessages id="yes" />
                        </Radio.Button>
                      </Radio.Group>
                    )}
                  </Form.Item>
                </Col>
                <Col span={18}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="itemTransfers.courier" />}
                  >
                    {getFieldDecorator("id_courier", {
                      initialValue: data.id_courier
                    })(
                      <Select
                        showSearch
                        allowClear
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="itemTransfers.courier" />
                        }
                        loading={loadingForeign}
                        optionFilterProp="children"
                        filterOption={true}
                        disabled={data.status !== 1 || data.transaction_type !== 1}
                      >
                        {couriers.map((data, key) => (
                          <Option key={key} value={data.id_courier}>
                            {`${data.courier_name} (${data.description})`}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
              </Col>
              <Col span={12} style={{ paddingLeft: "15px" }}>
                <InputGroup>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={
                        <IntlMessages id="purchaseInvoices.paymentCategory" />
                      }
                    >
                      {getFieldDecorator("id_payment_category", {
                        initialValue: data.id_payment_category,
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <Select
                          showSearch
                          style={{ width: "100%" }}
                          placeholder={
                            <IntlMessages id="purchaseInvoices.paymentCategory" />
                          }
                          onChange={() =>
                            setFieldsValue({
                              id_payment_term: undefined,
                              payment_due_date: undefined
                            })
                          }
                          loading={loadingForeign}
                          disabled={data.status !== 1 || data.transaction_type !== 1}
                          optionFilterProp="children"
                          filterOption={true}
                        >
                          {paymentCategories.map((data, key) => 
                            data.id_location === getFieldValue("id_location") || 
                            data.id_location === null ? (
                              <Option key={key} value={data.id_payment_category}>
                                {data.payment_category_code}
                              </Option>
                            ) : null
                          )}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="purchaseInvoices.paymentTerm" />}
                    >
                      {getFieldDecorator("id_payment_term", {
                        initialValue: data.id_payment_term,
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <Select
                          showSearch
                          style={{ width: "100%" }}
                          onSelect={this.handleChangePaymentTerm}
                          placeholder={
                            <IntlMessages id="purchaseInvoices.paymentTerm" />
                          }
                          loading={loadingForeign}
                          disabled={data.status !== 1 || data.transaction_type !== 1}
                          optionFilterProp="children"
                          filterOption={true}
                        >
                          {getFieldValue("id_payment_category")
                            ? paymentTerms.map((data, key) => {
                                const paymentCategory = paymentCategories.find(
                                  data =>
                                    data.id_payment_category ===
                                    getFieldValue("id_payment_category")
                                );
                                if (
                                  paymentCategory.payment_type === 2 &&
                                  data.is_cash === 0
                                ) {
                                  return (
                                    <Option
                                      key={key}
                                      value={data.id_payment_term}
                                    >
                                      {data.payment_term_code}
                                    </Option>
                                  );
                                } else if (
                                  paymentCategory.payment_type !== 2 &&
                                  data.is_cash === 1
                                ) {
                                  return (
                                    <Option
                                      key={key}
                                      value={data.id_payment_term}
                                    >
                                      {data.payment_term_code}
                                    </Option>
                                  );
                                }
                              })
                            : null}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>
                </InputGroup>
              </Col>
            </Row>
          </TabPane>
          <TabPane
            tab={<IntlMessages id="jobCostings.tab.attachment" />}
            key="3"
          >
            <PurchaseInvoiceDocUpdateForm
              submit={this.handleSubmit}
              purchaseInvoice={this.props.purchaseInvoice}
              params={this.props.params}
              form={this.props.form}
            />
          </TabPane>
        </Tabs>

        {this.state.tab !== "3" ? (
          <Form.Item {...tailFormItemLayout}>
            <Button
              type="primary"
              htmlType="submit"
              style={{ marginRight: "5px", marginTop: "24px" }}
              loading={loading || loadingSubmit}
              disabled={
                !permissions.includes("Update Purchase Invoice") ||
                data.status !== 1 || data.transaction_type !== 1 ||
                this.state.total_exclude_discount_tax - this.state.total_discount < 0
                  ? true
                  : false
              }
            >
              <IntlMessages id="button.submit" />
            </Button>

            {/* Confirm */}
            <Popconfirm
              title={<IntlMessages id="confirmTransaction" />}
              onConfirm={() =>
                this.props.confirmPurchaseInvoice(data.id_purchase_invoice)
              }
              okText={<IntlMessages id="yes" />}
              cancelText={<IntlMessages id="no" />}
            >
              <Button
                style={{ marginRight: "5px", padding: "0px 15px" }}
                disabled={
                  data.status !== 1 ||
                  !permissions.includes("Confirm Purchase Invoice")
                    ? true
                    : false
                }
                loading={loading || loadingSubmit}
              >
                <IntlMessages id="button.confirmTransaction" />
              </Button>
            </Popconfirm>

            {/* Cancel */}
            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={
                data.status !== 2 ||
                !permissions.includes("Cancel Purchase Invoice")
                  ? true
                  : false
              }
              loading={loading || loadingSubmit}
              onClick={this.handleModalCancel}
            >
              <IntlMessages id="button.cancelTransaction" />
            </Button>

            <br />

            {this.state.err ? (
              <span style={{ color: "#f5222d" }}>
                <IntlMessages id="form.error" />
              </span>
            ) : null}

            {this.state.total_exclude_discount_tax - this.state.total_discount < 0 ? (
              <IntlMessages id="discountExceed">
                {txt => <span style={{ color: "#f5222d" }}>{txt}</span>}
              </IntlMessages>
            ) : null}

            {this.state.err_item ? (
              <IntlMessages id="itemDataEmpty">
                {txt => <span style={{ color: "#f5222d" }}>{txt}</span>}
              </IntlMessages>
            ) : null}
          </Form.Item>
        ) : null}
      </Form>
    );
  }
}

const WrappedPurchaseInvoiceUpdateForm = Form.create({
  name: "update_purchase_invoice"
})(PurchaseInvoiceUpdateForm);

const mapStateToProps = state => {
  return {
    user: state.auth.user,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    {
      getItemsBySupplier,
      getDiscounts,
      getPurchaseReceipts,
      confirmPurchaseInvoice,
      cancelPurchaseInvoice,
      setTaxBase
    }
  )
)(WrappedPurchaseInvoiceUpdateForm);
