import React, { Component } from "react";

import { Button, Popconfirm, Tooltip, Form } from "antd";
import EditableCell from "./editableCell";
import EditableContext from "./editableContext";
import "../../../App/editableStyle.css";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper from "../../../App/app.style";
import ModalItemDetail from "./modalItemDetail";
import {
  getSubTotalPerItem,
  getNettPurchPrice,
  getItemPrice,
  getUnitConversion,
  getDiscountBillPerItem,
  getPurchasePrice
} from "../../../../helpers/helper";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

const getReceiptDiscount = row => {
  if (row.discount_per_unit && row.discount_per_unit.slice(-1) === "%") {
    return row.discount_per_unit;
  } else if (row.discount_per_unit) {
    return (parseFloat(row.discount_per_unit) * parseFloat(row.qty)).toString();
  } else if (row.discount) {
    return row.discount;
  } else if (row.item && row.item.discount) {
    return parseFloat(row.item.discount) * 100 + "%";
  } else {
    return "0";
  }
};

class EditableTable extends Component {
  constructor(props) {
    super(props);
    const { items } = props;

    this.state = {
      dataSource: [...items],
      count: items.length + 1,
      totalQty: 0,
      discount_pct: 0,
      discount_amount: 0,
      total_discount: 0,
      total_tax: 0,
      total_exclude_discount_tax: 0,
      visible: false,
      selectedItem: {},
      importedTransaction: 0,
    };
  }

  componentDidUpdate(prevProps) {
    const {
      loadingTransactions,
      loadingItems,
      item,
      items,
      currency_rate,
      suppDisc,
      // taxes,
      inclusive_tax,
      freightCosts,
      handleSaveItemList,
      clearItem,
      clearItems,
      clear
    } = this.props;

    // milih item sendiri
    if (!loadingTransactions && !loadingItems && item) {
      const { dataSource, count, totalQty } = this.state;
      const find = dataSource.findIndex(
        currData =>
          !currData.id_purchase_receipt && currData.id_item === item.id_item
      );
      // console.log(find);

      // item baru
      if (find < 0) {
        // const disc = item.discount ? item.discount : "0%";
        // const price = item.price ? parseFloat(item.price) : 0;

        let init = {
          key: dataSource.length,
          index_no: count,
          id_purchase_receipt: null,
          id_purchase_receipt_detail: null,
          purchase_receipt_no: null,
          id_purchase_order: null,
          purchase_order_no: null,
          id_item: item.id_item,
          item_code: item.item_code,
          item_name: item.item_name,
          qty: item.preference_item.purchase_qty
            ? parseFloat(item.preference_item.purchase_qty)
            : 1,
          base_qty: 1,
          id_unit: item.preference_item.id_purchase_unit,
          unit_code: item.preference_item.purchase_unit.unit_code,
          unit_conversion: item.unit_conversion,
          unit_conv: getUnitConversion(
            item.preference_item.id_purchase_unit,
            item.unit_conversion
          ),
          id_base_unit: item.id_base_unit,
          base_unit_code: item.base_unit.unit_code,
          id_tax: item.preference_item.id_purchase_tax,
          tax_pct: item.preference_item.purchase_tax.percentage,
          tax_name: item.preference_item.purchase_tax.tax_name,
          item_price: 0,
          location_price: item.location_price
            ? parseFloat(item.location_price)
            : 0,
          discount: "0",
          id_department: null,
          id_project: null,
          item_note: null,
          preference_item: {
            id_purchase_tax: item.preference_item.id_purchase_tax,
            purchase_tax: item.preference_item.purchase_tax,
            id_purchase_unit: item.preference_item.id_purchase_unit,
            purchase_unit: item.preference_item.purchase_unit
          },
          supplier_pricelists: item.supplier_pricelists
        };

        const price_list = getPurchasePrice(init, currency_rate);
        const subtotal = getSubTotalPerItem(
          price_list.price,
          1,
          price_list.discount
        );

        init.base_qty = init.qty * init.unit_conv;
        init.item_price = price_list.price;
        init.discount = price_list.discount;

        let newData = Object.assign({}, init, {
          netto_purchase_price: getNettPurchPrice(
            Object.assign({}, item, init),
            [...dataSource, init],
            inclusive_tax,
            suppDisc,
            freightCosts
          ),
          sub_total: subtotal
        });
        newData.bill_discount = getDiscountBillPerItem(
          newData,
          [...dataSource, newData],
          suppDisc
        );
        // console.log(newData)

        this.setState({
          dataSource: [...dataSource, newData],
          count: count + 1,
          totalQty: totalQty + 1
        });
        clearItem([...dataSource, newData], totalQty + 1);
        // console.log(this.props.item);
      }
      // penambahan 1 quantity jika item yang dipilih sudah ada
      else if (find >= 0) {
        let newTotQty = 0;
        const updateData = dataSource;
        let newData = Object.assign({}, updateData[find]);

        newData.qty = parseFloat(newData.qty) + 1;
        newData.base_qty = newData.qty * newData.unit_conv;
        newData.item_price = getItemPrice(newData);

        newData.sub_total = getSubTotalPerItem(
          newData.item_price,
          newData.qty,
          newData.discount
        );

        updateData.splice(find, 1, {
          ...updateData[find],
          ...newData
        });

        newData.bill_discount = getDiscountBillPerItem(
          newData,
          [...updateData],
          suppDisc
        );

        newData.netto_purchase_price = getNettPurchPrice(
          newData,
          [...updateData],
          inclusive_tax,
          suppDisc,
          freightCosts
        );

        updateData.splice(find, 1, {
          ...updateData[find],
          ...newData
        });
        updateData.map(
          currData => (newTotQty += parseFloat(currData.base_qty))
        );

        this.setState({
          dataSource: [...updateData],
          totalQty: newTotQty
        });
        clearItem(updateData, newTotQty);
        // console.log(this.props.item);
      }
    }
    // jika item dari transaksi sebelumnya
    else if (!loadingTransactions && !loadingItems && items.length !== 0) {
      let newData = [];
      let newTotQty = 0;
      let newIndex = 0;
      let importedTransaction = 0;

      items.map((item, key) => {
        const preference_item = item.item ? item.item.preference_item : {};
        const unit_conversion = item.unit_conversion
          ? item.unit_conversion
          : item.item.unit_conversion;
        const unit_conv = getUnitConversion(item.id_unit, unit_conversion);

        // counting imported transaction
        // untuk menentukan transaction_date
        if (item.id_purchase_receipt) {
          importedTransaction++;
        }

        let init = {
          key: key,
          index_no: item.index_no,
          id_purchase_receipt: item.id_purchase_receipt
            ? item.id_purchase_receipt
            : null,
          id_purchase_receipt_detail: item.id_purchase_receipt_detail
            ? item.id_purchase_receipt_detail
            : null,
          purchase_receipt_no: item.purchase_receipt_no
            ? item.purchase_receipt_no
            : null,
          id_purchase_order: item.id_purchase_order
            ? item.id_purchase_order
            : null,
          purchase_order_no: item.purchase_order_no
            ? item.purchase_order_no
            : null,
          id_item: item.id_item,
          item_code: item.item_code,
          item_name: item.item_name,
          qty: item.qty
            ? parseFloat(item.qty)
            : parseFloat(item.receive_qty) - parseFloat(item.returned_qty),
          base_qty: 1,
          id_unit: item.id_unit,
          unit_code: item.unit ? item.unit.unit_code : item.unit_code,
          unit_conversion: unit_conversion,
          unit_conv: unit_conv,
          id_base_unit: item.id_base_unit
            ? item.id_base_unit
            : item.item
              ? item.item.base_unit.id_unit
              : item.base_unit.id_unit,
          base_unit_code: item.base_unit_code
            ? item.base_unit_code
            : item.item
              ? item.item.base_unit.unit_code
              : item.base_unit.unit_code,
          id_tax: item.id_tax
            ? item.id_tax
            : item.tax
              ? item.tax.id_tax
              : item.item.preference_item.id_purchase_tax,
          tax_pct:
            item.tax_pct || item.tax_pct === 0
              ? item.tax_pct
              : item.tax
                ? parseFloat(item.tax.percentage)
                : parseFloat(item.item.preference_item.purchase_tax.percentage),
          tax_name: item.tax
            ? item.tax.tax_name
            : item.item
              ? item.item.preference_item
                ? item.item.preference_item.purchase_tax.tax_name
                : item.tax_name
              : item.tax_name,
          item_price:
            (parseFloat(item.item_price) === 0 || !item.item_price) &&
              item.po_detail
              ? parseFloat(item.po_detail.item_price)
              : item.item_price || item.item_price === 0
                ? parseFloat(item.item_price)
                : item.cost_per_unit
                  ? parseFloat(item.cost_per_unit) * unit_conv
                  : 0,
          discount_per_unit: item.discount_per_unit
            ? item.discount_per_unit
            : null,
          discount: item.discount,
          sub_total: 0,
          netto_purchase_price: 0,
          bill_discount: 0,
          id_department: item.id_department,
          id_project: item.id_project,
          item_note: item.item_note,
          preference_item: item.preference_item
            ? item.preference_item
            : {
              id_purchase_tax: preference_item.id_purchase_tax,
              purchase_tax: preference_item.purchase_tax,
              id_purchase_unit: preference_item.id_purchase_unit,
              purchase_unit: preference_item.purchase_unit
            },
          supplier_pricelists: item.supplier_pricelists
            ? item.supplier_pricelists
            : item.item
              ? item.item.supplier_pricelists
                ? item.item.supplier_pricelists
                : []
              : [],
          po_detail: item.po_detail ? item.po_detail : []
        };

        init.base_qty = init.qty * init.unit_conv;
        init.discount = getReceiptDiscount(init);

        init.sub_total = getSubTotalPerItem(
          init.item_price,
          init.qty,
          init.discount
        );

        newTotQty += parseFloat(item.base_qty);
        newIndex = item.index_no + 1;
        newData.push(init);
      });

      newData.map(currData => {
        currData.bill_discount = getDiscountBillPerItem(
          currData,
          [...newData],
          suppDisc
        );

        currData.netto_purchase_price = getNettPurchPrice(
          currData,
          [...newData],
          inclusive_tax,
          suppDisc,
          freightCosts
        );
      });
      // console.log(newData);
      this.setState({
        dataSource: [...newData],
        totalQty: newTotQty,
        count: newIndex,
        importedTransaction: importedTransaction
      });
      clearItems(newData, newTotQty, importedTransaction);
    }
    // Jika inclusive tax
    else if (prevProps.inclusive_tax !== inclusive_tax) {
      let newData = this.state.dataSource;
      const { importedTransaction } = this.state
      // const sumSubtotal = getSumSubtotal(newData);

      newData.map(data => {
        data.bill_discount = getDiscountBillPerItem(data, newData, suppDisc);
        data.netto_purchase_price = getNettPurchPrice(
          data,
          newData,
          inclusive_tax,
          suppDisc,
          freightCosts
        );
      });
      this.setState({ dataSource: [...newData] });
      handleSaveItemList(newData, this.state.totalQty, false, importedTransaction);
    } else if (prevProps.suppDisc !== suppDisc) {
      let newData = this.state.dataSource;
      const { importedTransaction } = this.state
      // const sumSubtotal = getSumSubtotal(newData);

      newData.map(data => {
        data.bill_discount = getDiscountBillPerItem(data, newData, suppDisc);
        data.netto_purchase_price = getNettPurchPrice(
          data,
          newData,
          inclusive_tax,
          suppDisc,
          freightCosts
        );
      });
      this.setState({ dataSource: [...newData] });
      handleSaveItemList(newData, this.state.totalQty, false, importedTransaction);
    } else if (prevProps.freightCosts !== freightCosts) {
      let newData = this.state.dataSource;
      const { importedTransaction } = this.state

      newData.map(data => {
        data.netto_purchase_price = getNettPurchPrice(
          data,
          newData,
          inclusive_tax,
          suppDisc,
          freightCosts
        );
      });

      if (newData !== this.state.dataSource) {
        this.setState({ dataSource: [...newData] });
        handleSaveItemList(newData, this.state.totalQty, false, importedTransaction);
      }
    }
    // clear item jika location/supplier berubah
    else if (clear) {
      this.setState({ dataSource: [], count: 1, importedTransaction: 0 });
      handleSaveItemList([], 0, false, 0);
    }
  }

  handleItemDetailModal = row => {
    const { visible } = this.state;
    // console.log(row);

    visible
      ? this.setState({ visible: false, selectedItem: {} })
      : this.setState({ visible: true, selectedItem: row });
  };

  handleItemDetail = value => {
    this.setState({ visible: false, selectedItem: {} });
    this.handleSave(value);
  };

  handleDelete = index_no => {
    const { handleSaveItemList } = this.props;
    let newTotQty = 0;

    const dataSource = this.state.dataSource;

    const deleted = dataSource.find(
      currData => currData.index_no === index_no
    );
    let newData = null;
    if (deleted.id_purchase_receipt) {
      newData = dataSource.filter(
        currData =>
          currData.index_no !== index_no &&
          currData.id_purchase_receipt !== deleted.id_purchase_receipt
      );
    } else {
      newData = dataSource.filter(currData => currData.index_no !== index_no);
    }

    let importedTransaction = 0;
    newData.map((currData, index) => {
      currData.key = index;
      newTotQty += parseFloat(currData.base_qty);

      // counting imported transaction
      // untuk menentukan transaction_date
      if (currData.id_purchase_receipt) {
        importedTransaction++;
      }
    });

    this.setState({
      dataSource: [...newData],
      // count: newData.length + 1,
      totalQty: newTotQty,
      importedTransaction
    });
    handleSaveItemList(newData, newTotQty, true, importedTransaction);
  };

  handleSave = row => {
    const {
      handleSaveItemList,
      inclusive_tax,
      suppDisc,
      currency_rate,
      freightCosts
    } = this.props;
    const newData = this.state.dataSource;
    const { importedTransaction } = this.state
    let newTotQty = 0;

    const index = newData.findIndex(
      currData => row.index_no === currData.index_no
    );

    row.unit_conv = getUnitConversion(row.id_unit, row.unit_conversion);
    if (row.id_unit !== newData[index].id_unit) {
      const price_list = getPurchasePrice(row, currency_rate);
      row.item_price = price_list.price;
      row.discount = price_list.discount;
    } else {
      row.discount = row.discount ? row.discount : "0";
    }
    row.base_qty = row.qty * row.unit_conv;
    // row.item_price = getItemPrice(row);
    row.sub_total = getSubTotalPerItem(row.item_price, row.qty, row.discount);

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    row.bill_discount = getDiscountBillPerItem(row, newData, suppDisc);
    row.netto_purchase_price = getNettPurchPrice(
      row,
      newData,
      inclusive_tax,
      suppDisc,
      freightCosts
    );

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    newData.map(currData => (newTotQty += parseFloat(currData.base_qty)));

    // console.log(row.base_qty);

    this.setState({ dataSource: [...newData], totalQty: newTotQty });
    handleSaveItemList(newData, newTotQty, false, importedTransaction);
  };

  render() {
    // console.log(this.state.dataSource);

    const { dataSource, visible, selectedItem } = this.state;
    const { loadingTable } = this.props;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const raw_columns = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "key",
        width: "5%",
        render: text => `${text + 1}`
        // fixed: "left"
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="purchaseReceipts.purchaseReceiptNo" />}>
            <a href="javascript:;"><IntlMessages id="prNo" /> </a>
            {/* <br />
            <Icon type="question-circle" /> */}
          </Tooltip>
        ),
        dataIndex: "purchase_receipt_no",
        width: "10%",
        render: text =>
          text && text.length > 10 ? (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 10) + "..."}
            </Tooltip>
          ) : text ? (
            text
          ) : (
                "-"
              )
      },
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "item_code",
        width: "10%",
        // fixed: "left",
        render: text => {
          if (text && text.length > 10) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 10) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "quantity" }),
        dataIndex: "qty",
        width: "15%",
        align: "right",
        editable: true
      },
      {
        title: getLanguage({ id: "unit" }),
        width: "10%",
        dataIndex: "unit_code",
        align: "center",
        editable: true
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="baseQuantity" />}>
            <a href="javascript:;"><IntlMessages id="baseQty" /> </a>
            {/* <br />
            <Icon type="question-circle" /> */}
          </Tooltip>
        ),
        width: "15%",
        dataIndex: "base_qty",
        align: "right",
        render: (text, row) => <><NumberFormat value={text} /> {row.base_unit_code}</>
      },
      {
        title: getLanguage({ id: "price" }),
        dataIndex: "item_price",
        width: "15%",
        align: "right",
        editable: true
      },
      {
        title: getLanguage({ id: "tax" }),
        dataIndex: "tax_name",
        width: "10%",
        align: "center",
        editable: true
      },
      {
        title: getLanguage({ id: "discount" }),
        dataIndex: "discount",
        width: "15%",
        align: "right",
        editable: true
      },
      {
        title: getLanguage({ id: "subTotal" }),
        dataIndex: "sub_total",
        width: "15%",
        // editable: true,
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "netPurchasePrice" }),
        dataIndex: "netto_purchase_price",
        width: "15%",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: "",
        dataIndex: "operation",
        width: "15%",
        align: "center",
        fixed: "right",
        render: (text, row) => {
          if (!this.props.status || this.props.status === 1) {
            return (
              <div>
                <Tooltip
                  placement="top"
                  title={<IntlMessages id="detail" />}
                >
                  <Button
                    //style={{ borderRadius: "50%" }}
                    className="invoiceEdtBtn"
                    size="small"
                    onClick={() => this.handleItemDetailModal(row)}
                  >
                    <i className="ion-navicon-round" />

                  </Button>
                </Tooltip>
         
                <Popconfirm
                  title={<IntlMessages id="confirmDelete" />}
                  onConfirm={() => this.handleDelete(row.index_no)}
                  okText={<IntlMessages id="yes" />}
                  cancelText={<IntlMessages id="no" />}
                >
                  <Tooltip
                    title={<IntlMessages id="delete" />}
                  >
                    <Button
                      //style={{ borderRadius: "50%" }}
                      className="invoiceDltBtn"
                      size="small"
                    >
                      <i className="ion-trash-b" />
                    </Button>
                  </Tooltip>
                </Popconfirm>
              </div>
            );
          } else {
            return (
              <Tooltip
                placement="top"
                title={<IntlMessages id="detail" />}
              >
                <Button
                  //style={{ borderRadius: "50%" }}
                  className="invoiceEdtBtn"
                  size="small"
                  onClick={() => this.handleItemDetailModal(row)}
                >
                  <i className="ion-navicon-round" />

                </Button>
              </Tooltip>
            );
          }
        }
      }
    ];

    const columns = raw_columns.map(col => {
      if (!col.editable) {
        return col;
      }
      return {
        ...col,
        onCell: record => ({
          record,
          editable: col.editable,
          dataIndex: col.dataIndex,
          title: col.title,
          taxes: this.props.taxes,
          status: this.props.status,
          permissions: this.props.permissions,
          handleSave: this.handleSave
        })
      };
    });
    return (
      <CardWrapper>
        <ModalItemDetail
          visible={visible}
          selectedData={selectedItem}
          status={this.props.status}
          handleItemDetailModal={this.handleItemDetailModal}
          handleItemDetail={this.handleItemDetail}
          submit={this.handleItemDetail}
          departments={this.props.departments}
          projects={this.props.projects}
        />

        <Scrollbars style={{ width: "100%" }}>
          <TableWrapper
            className="invoiceListTable"
            components={components}
            rowKey={record => record.index_no}
            rowClassName={() => "editable-row"}
            dataSource={dataSource}
            columns={columns}
            loading={loadingTable}
            pagination={false}
          // scroll={{ x: 1300 }}
          />
        </Scrollbars>
      </CardWrapper>
    );
  }
}

export default EditableTable;
