import React, { Component } from "react";
import { Form, Input, InputNumber, Select, Tooltip } from "antd";
import EditableContext from "./editableContext";
import IntlMessages from "../../../../components/utility/intlMessages";
import { parseDiscount } from "../../../../helpers/helper";
import NumberFormat from "../../../App/NumberFormat";

const FormItem = Form.Item;
const Option = Select.Option;

class EditableCell extends Component {
  state = {
    editing: false
  };

  toggleEdit = () => {
    const editing = !this.state.editing;

    this.setState({ editing }, () => {
      if (editing) {
        this.input.focus();
      }
    });
  };

  handleClickOutside = e => {
    const { editing } = this.state;

    if (editing && !this.cell) {this.toggleEdit();} else if (editing && this.cell !== e.target && !this.cell.contains(e.target)) {
      this.save();
      document.removeEventListener("click", this.handleClickOutside, true);
    }
  };

  save = (name, id) => {
    const { record, taxes, handleSave } = this.props;

    this.form.validateFields((error, values) => {
      if (error) {
        return;
      }

      this.toggleEdit();
      if (name && name === "unit_code") {
        const unit_conversion = record.unit_conversion.find(
          currData => currData.id_unit === id
        );

        const newRecord = Object.assign({}, record, {
          id_unit: unit_conversion ? unit_conversion.unit.id_unit : null,
          unit_code: unit_conversion ? unit_conversion.unit.unit_code : "-"
        });

        handleSave(newRecord);
      } else if (name && name === "tax_name") {
        const tax = taxes.find(currData => currData.id_tax === id);
        const newRecord = Object.assign({}, record, {
          id_tax: id,
          tax_name: tax.tax_name,
          tax_pct: tax.percentage
        });
        handleSave(newRecord);
      } else if (values.discount) {
        // normalisasi diskon
        const subtotal = parseFloat(record.qty) * parseFloat(record.item_price);
        if (
          values.discount.slice(-1) === "%" &&
          parseFloat(parseDiscount(values.discount)) > 100
        ) {
          values.discount = "100%";
        } else if (
          values.discount.slice(-1) !== "%" &&
          parseFloat(parseDiscount(values.discount)) > subtotal
        ) {
          values.discount = `${subtotal}`;
        } else if (values.discount.slice(-1) === "%") {
          values.discount = `${parseDiscount(values.discount)}%`;
        } else {
          values.discount = parseDiscount(values.discount);
        }
        handleSave({ ...record, ...values });
      } else {
        handleSave({ ...record, ...values });
      }
    });
  };

  render() {
    const { editing } = this.state;
    const {
      editable,
      dataIndex,
      title,
      record,
      index,
      handleSave,
      taxes,
      status,
      permissions,
      ...restProps
    } = this.props;
    return (
      <td ref={node => (this.cell = node)} {...restProps}>
        {editable ? (
          <EditableContext.Consumer>
            {form => {
              this.form = form;
              if (
                editing &&
                (dataIndex === "qty" || dataIndex === "item_price")
              ) {
                document.addEventListener(
                  "click",
                  this.handleClickOutside,
                  true
                );
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ],
                      initialValue: record[dataIndex]
                    })(
                      <InputNumber
                        ref={node => (this.input = node)}
                        onPressEnter={this.save}
                        formatter={value =>
                          `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={value => value.replace(/\$\s?|(,*)/g, "")}
                        min={0.01}
                        precision={2}
                      />
                    )}
                  </FormItem>
                );
              } else if (editing && dataIndex === "unit_code") {
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      initialValue: record.id_unit
                    })(
                      <Select
                        ref={node => (this.input = node)}
                        showSearch
                        style={{ width: "100%" }}
                        onSelect={this.save.bind(this, "unit_code")}
                        optionFilterProp="children"
                        filterOption={true}
                      >
                        {record.unit_conversion.map((data, key) => (
                          <Option
                            key={data.unit.id_unit}
                            value={data.unit.id_unit}
                          >
                            {data.unit.unit_code}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </FormItem>
                );
              } else if (editing && dataIndex === "tax_name") {
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      initialValue: record.id_tax
                    })(
                      <Select
                        ref={node => (this.input = node)}
                        showSearch
                        style={{ width: "100%" }}
                        onSelect={this.save.bind(this, "tax_name")}
                        optionFilterProp="children"
                        filterOption={true}
                      >
                        {taxes.map((data, key) => (
                          <Option key={data.id_tax} value={data.id_tax}>
                            {data.tax_name}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </FormItem>
                );
              } else if (editing && dataIndex === "discount") {
                document.addEventListener(
                  "click",
                  this.handleClickOutside,
                  true
                );
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      initialValue: record[dataIndex]
                    })(
                      <Input
                        style={{ width: "100px" }}
                        ref={node => (this.input = node)}
                        onPressEnter={this.save}
                      />
                    )}
                  </FormItem>
                );
              } else if (
                !editing &&
                dataIndex === "item_price" &&
                status === 1
              ) {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {record[dataIndex]
                      ? <NumberFormat value={record[dataIndex]} />
                      : "0"}
                  </div>
                );
              } else if (
                !editing &&
                dataIndex === "item_price" &&
                status !== 1
              ) {
                return record[dataIndex]
                  ? <NumberFormat value={record[dataIndex]} />
                  : "0";
              } else if (
                !editing &&
                dataIndex === "qty" &&
                !record.id_purchase_receipt &&
                status === 1
              ) {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {record[dataIndex]
                      ? <NumberFormat value={record[dataIndex]} />
                      : "0"}
                  </div>
                );
              } else if (
                !editing &&
                dataIndex === "qty" &&
                !record.id_purchase_receipt &&
                status !== 1
              ) {
                return record[dataIndex]
                  ? <NumberFormat value={record[dataIndex]} />
                  : "0";
              } else if (
                !editing &&
                dataIndex === "qty" &&
                record.id_purchase_receipt
              ) {
                return restProps.children;
              } else if (
                !editing &&
                dataIndex === "unit_code" &&
                !record.id_purchase_receipt &&
                status === 1
              ) {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {restProps.children}
                  </div>
                );
              } else if (
                !editing &&
                dataIndex === "unit_code" &&
                !record.id_purchase_receipt &&
                status !== 1
              ) {
                return restProps.children;
              } else if (
                !editing &&
                dataIndex === "unit_code" &&
                record.id_purchase_receipt
              ) {
                return restProps.children;
              } else if (
                !editing &&
                dataIndex === "discount" &&
                status === 1 &&
                permissions.includes("Edit Discount Purchase Invoice")
              ) {
                return (
                  <Tooltip
                    placement="bottom"
                    title={<IntlMessages id="purchaseOrders.discount.help" />}
                  >
                    {record.sub_total < 0 && parseFloat(record.discount) > record.sub_total ? (
                      <>
                        <div
                          className="editable-cell-value-wrap error"
                          onClick={this.toggleEdit}
                        >
                          {record[dataIndex] && record[dataIndex].slice(-1) === "%" 
                            ? <><NumberFormat value={parseFloat(record[dataIndex])} />%</>
                            : <NumberFormat value={record[dataIndex]} />}
                        </div>
                        <IntlMessages id="discountExceed">
                          {txt => <span style={{ color: "#f5222d", fontSize: ".9em" }}>{txt}</span>}
                        </IntlMessages>
                      </>
                    ) : (
                      <div
                        className="editable-cell-value-wrap"
                        onClick={this.toggleEdit}
                      >
                        {record[dataIndex] && record[dataIndex].slice(-1) === "%" 
                            ? <><NumberFormat value={parseFloat(record[dataIndex])} />%</>
                            : <NumberFormat value={record[dataIndex]} />}
                      </div>
                    )}
                  </Tooltip>
                );
              } else if (
                !editing &&
                dataIndex === "discount" &&
                status === 1 &&
                !permissions.includes("Edit Discount Purchase Invoice")
              ) {
                return record[dataIndex] && record[dataIndex].slice(-1) === "%" 
                  ? <><NumberFormat value={parseFloat(record[dataIndex])} />%</>
                  : <NumberFormat value={record[dataIndex]} />;
              } else if (!editing && dataIndex === "discount" && status !== 1) {
                return record[dataIndex] && record[dataIndex].slice(-1) === "%" 
                  ? <><NumberFormat value={parseFloat(record[dataIndex])} />%</>
                  : <NumberFormat value={record[dataIndex]} />;
              } else if (status !== 1) {
                return restProps.children;
              } else {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    style={{ paddingRight: 24 }}
                    onClick={this.toggleEdit}
                  >
                    {restProps.children}
                  </div>
                );
              }
            }}
          </EditableContext.Consumer>
        ) : (
          restProps.children
        )}
      </td>
    );
  }
}

export default EditableCell;
