import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import PurchaseInvoiceCreateForm from "./createForm";
import notification from "../../../../components/notification";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/purchase/purchaseInvoice/actions";
import { Box } from "../../../App/app.style";
import { removeSaveData } from "../../../../helpers/helper";

const redirecTo = "/dashboard/purchases/purchase-invoices";

class CreatePurchaseInvoice extends Component {
  componentDidMount() {
    const { createPurchaseInvoice } = this.props;
    createPurchaseInvoice();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.purchaseInvoice.error) {
      notification("error", this.props.purchaseInvoice.error);
      this.props.clearError();
    }
    if (
      (this.props.purchaseInvoice.success ||
        this.props.purchaseInvoice.warning) &&
      this.props.purchaseInvoice.id_purchase_invoice
    ) {
      this.props.history.replace(
        redirecTo + "/update/" + this.props.purchaseInvoice.id_purchase_invoice
      );
      // this.props.history.replace(redirecTo);
      // notification("success", this.props.purchaseInvoice.success);
      // this.props.clearSuccess();
    }
  }

  handleSubmit = value => {
    const { storePurchaseInvoice } = this.props;
    storePurchaseInvoice(value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="purchaseInvoices.button.add" />}>
            <Button
              onClick={() => {
                removeSaveData("pi");
                this.props.history.replace(redirecTo);
              }}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <PurchaseInvoiceCreateForm
            submit={this.handleSubmit}
            purchaseInvoice={this.props.purchaseInvoice}
            history={this.props.history}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    purchaseInvoice: state.purchaseInvoice,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  CreatePurchaseInvoice
);
