import React, { Component } from "react";
import { Form, InputNumber, Select, Empty, Tag, Tooltip } from "antd";
import EditableContext from "./editableContext";
import IntlMessages from "../../../../components/utility/intlMessages";
import NumberFormat from "../../../App/NumberFormat";

const FormItem = Form.Item;
const Option = Select.Option;

const filterNewItems = (items, arr_items) => {
  let filtered = [];
  let selected_items = []
  if (typeof arr_items[0] === "string") {
    arr_items.map(id_item => {
      const item = items.find(data => data.id_item === id_item);
      if (item) {
        filtered.push(id_item);
        selected_items.push(item)
      }
    });
  } else {
    arr_items.map(currData => {
      const item = items.find(data => data.index_no === currData.index_no);
      if (item) {
        filtered.push({
          index_no: currData.index_no,
          id_item: currData.id_item
        });
        selected_items.push(item)
      }
    });
  }

  return { filtered, selected_items};
};

class EditableCell extends Component {
  state = {
    editing: false,
    freight_cost_items: this.props.record
      ? this.props.record.freight_cost_items
        ? this.props.record.freight_cost_items
        : []
      : [],
    selected_items: []
  };

  componentDidUpdate(prevProps) {
    if (prevProps.items !== this.props.items) {
      const { freight_cost_items } = this.state;
      const {record, handleSave} = this.props;

      const filtered = filterNewItems(this.props.items, freight_cost_items);
      this.setState({
        freight_cost_items: filtered.filtered,
        selected_items: filtered.selected_items
      });

      const newRecord = Object.assign({}, record, {
        freight_cost_items: filtered.filtered,
        selected_items: filtered.selected_items
      });
      
      handleSave(newRecord);
    }
  }

  toggleEdit = () => {
    const editing = !this.state.editing;

    this.setState({ editing }, () => {
      if (editing) {
        this.input.focus();
      }
    });
  };

  handleClickOutside = e => {
    const { editing } = this.state;

    if (editing && !this.cell) {this.toggleEdit();} else if (editing && this.cell !== e.target && !this.cell.contains(e.target)) {
      this.save();
      document.removeEventListener("click", this.handleClickOutside, true);
    }
  };

  save = (name, id) => {
    const { record, handleSave, items } = this.props;

    this.form.validateFields((error, values) => {
      if (error) {
        return;
      }

      if (name && name === "item_name") {
        let arr_items = [];
        let selected_items = [];

        if (id.includes("all")) {
          items.map(data =>
            arr_items.push({ index_no: data.index_no, id_item: data.id_item })
          );
          selected_items = items;

          this.setState({
            freight_cost_items: arr_items,
            selected_items: selected_items
          });
        } else {
          items.map(currData => {
            if (id.includes(currData.index_no)) {
              selected_items.push(currData);
              arr_items.push({
                index_no: currData.index_no,
                id_item: currData.id_item
              });
            }
          });

          this.setState({
            freight_cost_items: arr_items,
            selected_items: selected_items
          });
        }

      } else {
        this.toggleEdit();
        handleSave({ ...record, ...values });
      }
    });
  };

  handleClickOutsideMultiple = e => {
    const { editing } = this.state;

    if (editing && !this.cell) this.toggleEdit();

    // ketika klik tombol clear
    else if(editing && this.cell && this.cell.contains(e.target)){
      this.saveMultiple();
    }

    // ketika klik option
    else if(editing && this.cell && e.target.getAttribute("role") === "option") {
      this.saveMultiple();
    }

    // ketika klik diluar
    else if(editing && this.cell && !this.cell.contains(e.target)) {
      this.saveMultiple();
      this.toggleEdit();
      document.removeEventListener("click", this.handleClickOutsideMultiple, true);
    }
  }

  saveMultiple = () => {
    const {freight_cost_items, selected_items} = this.state;
    const {record, handleSave} = this.props;
    const newRecord = Object.assign({}, record, {
      freight_cost_items: freight_cost_items,
      selected_items: selected_items
    });
    
    handleSave(newRecord);
  }

  render() {
    const { editing } = this.state;
    const {
      editable,
      dataIndex,
      title,
      record,
      index,
      handleSave,
      items,
      status,
      ...restProps
    } = this.props;
    return (
      <td ref={node => (this.cell = node)} {...restProps}>
        {editable ? (
          <EditableContext.Consumer>
            {form => {
              this.form = form;
              if (editing && dataIndex === "currency_amount") {
                document.addEventListener(
                  "click",
                  this.handleClickOutside,
                  true
                );
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ],
                      initialValue: record[dataIndex]
                    })(
                      <InputNumber
                        ref={node => (this.input = node)}
                        style={{ width: "100%" }}
                        onPressEnter={this.save}
                        formatter={value =>
                          `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={value => value.replace(/\$\s?|(,*)/g, "")}
                        min={0.01}
                        precision={2}
                      />
                    )}
                  </FormItem>
                );
              } else if (editing && dataIndex === "item_name") {
                document.addEventListener(
                  "click",
                  this.handleClickOutsideMultiple,
                  true
                );
                return (
                  <Select
                    ref={node => (this.input = node)}
                    mode="multiple"
                    showSearch
                    allowClear={true}
                    optionFilterProp="children"
                    onChange={this.save.bind(this, "item_name")}
                    value={this.state.freight_cost_items.map(
                      data => data["index_no"]
                    )}
                    notFoundContent={
                      items.length > 0 ? (
                        <Empty description={<IntlMessages id="noData" />} />
                      ) : (
                        <Empty description={<IntlMessages id="noItems" />} />
                      )
                    }
                    dropdownMatchSelectWidth={false}
                    //disabled={used ? true : false}
                  >
                    {items.length > 0 ? (
                      <Option value={"all"}>
                        <IntlMessages id="select.all.item">
                          {txt => txt}
                        </IntlMessages>
                      </Option>
                    ) : null}

                    {Array.isArray(items)
                      ? items.map((data, key) => (
                          <Option key={key} value={data.index_no}>
                            {`${data.item_code} (${data.item_name})`}
                          </Option>
                        ))
                      : null}
                  </Select>
                );
              } else if (
                !editing &&
                dataIndex === "currency_amount" &&
                status === 1
              ) {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {record[dataIndex]
                      ? <NumberFormat value={record[dataIndex]} />
                      : "0"}
                  </div>
                );
              } else if (
                !editing &&
                dataIndex === "currency_amount" &&
                status !== 1
              ) {
                return record[dataIndex]
                  ? <NumberFormat value={record[dataIndex]} />
                  : "0";
              } else if (
                !editing &&
                dataIndex === "item_name" &&
                status === 1
              ) {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {Array.isArray(record.selected_items) ? (
                      record.selected_items.length > 0 ? (
                        record.selected_items.map((data, key) => {
                          const isLongTag = data.item_name.length > 15;
                          const tagElem = (
                            <Tag key={key} closable={false}>
                              {isLongTag
                                ? `${data.item_name.slice(0, 15)}...`
                                : data.item_name}
                            </Tag>
                          );

                          return isLongTag ? (
                            <Tooltip title={data.item_name} key={key}>
                              {tagElem}
                            </Tooltip>
                          ) : (
                            tagElem
                          );
                        })
                      ) : (
                        <IntlMessages id="purchaseInvoices.item.placeholder" />
                      )
                    ) : (
                      <IntlMessages id="purchaseInvoices.item.placeholder" />
                    )}
                  </div>
                );
              } else if (
                !editing &&
                dataIndex === "item_name" &&
                status !== 1
              ) {
                return Array.isArray(record.selected_items)
                  ? record.selected_items.map((data, key) => {
                      const isLongTag = data.item_name.length > 15;
                      const tagElem = (
                        <Tag key={key} closable={false}>
                          {isLongTag
                            ? `${data.item_name.slice(0, 15)}...`
                            : data.item_name}
                        </Tag>
                      );

                      return isLongTag ? (
                        <Tooltip title={data.item_name} key={key}>
                          {tagElem}
                        </Tooltip>
                      ) : (
                        tagElem
                      );
                    })
                  : "-";
              } else if (status !== 1) {
                return restProps.children;
              } else {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {restProps.children}
                  </div>
                );
              }
            }}
          </EditableContext.Consumer>
        ) : (
          restProps.children
        )}
      </td>
    );
  }
}

export default EditableCell;
