import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Col, Row, Modal } from "antd";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
const { TextArea } = Input;

class ModalItemDetail extends Component {
  componentDidUpdate(prevProps) {
    const { selectedData } = this.props;
    const { setFieldsValue } = this.props.form;

    if (prevProps.selectedData !== selectedData) {
      setFieldsValue({
        id_department: selectedData.id_department
          ? selectedData.id_department
          : null,
        id_project: selectedData.id_project ? selectedData.id_project : null,
        item_note: selectedData.item_note ? selectedData.item_note : null
      });
    }
  }
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        const { selectedData } = this.props;

        values.id_department = values.id_department ? values.id_department : null;
        values.id_project = values.id_project ? values.id_project : null;
        values.item_note = values.item_note ? values.item_note : null;
        
        const data = Object.assign({}, selectedData, values);
        // console.log(data);
        this.props.submit(data);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator } = this.props.form;
    const { departments, projects, status } = this.props;
    // const error = pettyCashType.errorField ? pettyCashType.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="itemRequests.itemDetail" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleItemDetailModal}
        onOk={this.handleSubmit}
        width="50%"
        okText={getLanguage({ id: "button.ok" })}
        cancelText={getLanguage({ id: "button.cancel" })}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Row type="flex" justify="start" gutter={6}>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="itemRequests.department" />}
              >
                {getFieldDecorator("id_department", {
                  //   initialValue: selectedData.id_department
                })(
                  <Select
                    allowClear
                    showSearch
                    placeholder={<IntlMessages id="itemRequests.department" />}
                    disabled={
                      status !== 1 ||
                      this.props.selectedData.id_purchase_receipt
                    }
                    optionFilterProp="children"
                    filterOption={true}
                  >
                    {departments.map((data, key) => (
                      <Option key={key} value={data.id_department}>
                        {data.department_name}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="itemRequests.project" />}
              >
                {getFieldDecorator("id_project", {
                  //   initialValue: selectedData.id_project
                })(
                  <Select
                    showSearch
                    allowClear
                    placeholder={<IntlMessages id="itemRequests.project" />}
                    disabled={
                      status !== 1 ||
                      this.props.selectedData.id_purchase_receipt
                    }
                    optionFilterProp="children"
                    filterOption={true}
                  >
                    {projects.map((data, key) => (
                      <Option key={key} value={data.id_project}>
                        {data.project_name}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Form.Item
                {...formTextArea}
                label={<IntlMessages id="itemRequests.itemNotes" />}
              >
                {getFieldDecorator("item_note", {
                  //   initialValue: selectedData.item_note
                })(
                  <TextArea
                    disabled={
                      status !== 1 ||
                      this.props.selectedData.id_purchase_receipt
                    }
                    // placeholder="Notes"
                    rows={5}
                  />
                )}
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Modal>
    );
  }
}

const WrappedItemDetailModal = Form.create({ name: "item_detail_modal" })(
  ModalItemDetail
);

export default WrappedItemDetailModal;
